let initState = sessionStorage.getItem('ddl')
if (initState) initState = JSON.parse(initState)
else initState = {
  airPortOptions: [],
  airlineOptions: [],
  airTypeOptions: [],
  flightStatusOptions: [],
  approveStatusOptions: [],
  flightNoOptions: [],
  callsignOptions: [],
  arrDepOptions: [],
  airportDOAOptions: [],
  traffTypeOptions: [],
  parkingBayOptions: { listBay: [], listSlot: [] },
  zoneOptions: [],
  countryOptions: [],
  regionOptions: [],
  beltOptions: [],
  terminalOptions: [],
  gateOptions: [],
  prefixOptions: [],
  groupOptions: [],
  airportTypeOptions: [],
  reasonFlightStatusOptions: [],
  countryInZoneOptions: [],
  columnTypeOptions: [
    { id: 1, text: 'Integer' },
    { id: 2, text: 'Decimal' },
    { id: 3, text: 'Text' },
    { id: 4, text: 'Date Time' },
  ],
  priceUnit: [
    { id: 1, text: 'Per ton' },
    { id: 2, text: 'Bulk' }
  ],
  yearOptions: [
    { id: 2018, text: 2018 },
    { id: 2019, text: 2019 },
    { id: 2020, text: 2020 },
    { id: 2021, text: 2021 },
    { id: 2022, text: 2022 },
  ]
}

const createAirlineCode = data => {
  let result = []
  for (let d of data) {
    if (d.codeICAO !== "" && result.indexOf(d.codeICAO) === -1) result.push(d.codeICAO)
    if (d.codeIATA !== "" && result.indexOf(d.codeIATA) === -1) result.push(d.codeIATA)
  }
  return result.map((r, i) => ({ id: i, text: r }))
}

export default (state = initState, action) => {
  switch (action.type) {
    case 'SET_DROPDOWN_OPTIONS':
      state[action.key] = action.data
      if (action.key === 'airlineOptions') {
        state['airlineCodeOptions'] = createAirlineCode(action.data)
      }
      sessionStorage.setItem('ddl', JSON.stringify(state))
      return { ...state }
    default:
      return state
  }
}
