import React from 'react'
import {
  Input,
  Popover,
  DatePicker,
  Radio,
  Checkbox,
  TimePicker,
  Switch,
  Icon,
} from 'antd'
import moment from 'moment'
import TextArea from 'antd/lib/input/TextArea'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
// import localeTH from 'antd/lib/date-picker/locale/th_TH'
import localeEN from 'antd/lib/date-picker/locale/en_US'
import DynamicDropdown from './DynamicDropdown'
import validatejs from 'validate.js'

const { RangePicker } = DatePicker
const Search = Input.Search
const defaultFormat = 'YYYYMMDD'

const validate = (data, value) => {
  let valid = undefined
  if (data.constraint) {
    valid = validatejs({ [data.name]: value === undefined ? data.value : value }, data.constraint)
  }
  if (valid) return valid[data.name]
  return valid
}

const wrapup = (data, input) => {
  if (!data.addon) return input
  if (data.addon.addonAfter) return input
  return <span className="ant-input-wrapper ant-input-group" style={{ maxHeight: 32, position: 'relative', tableLayout: 'fixed' }}>
    {input}
    <Popover
      id={data.addon.id || "popover"}
      content={data.addon.detail}
      title={data.addon.title}
      trigger={data.addon.trigger || 'hover'}>
      <span className="ant-input-group-addon" style={{ width: 32, height: 32 }}>
        <FontAwesomeIcon icon={data.addon.icon} />
      </span>
    </Popover>
  </span>
}

const renderDatepicker = (data, onChange) => {
  const valid = validate(data)
  const format = data.format || defaultFormat
  const render = <DatePicker
    style={{ width: '100%', display: 'table-cell' }}
    name={data.name}
    locale={localeEN}
    className={valid === undefined ? '' : "has-error"}
    placeholder={data.placeholder}
    format={format}
    disabled={data.disabled}
    onChange={(date, dateString) =>
      onChange({ name: data.name, value: dateString, error: validate(data, dateString) })}
    value={data.value ? moment(data.value, format) : null} // with format use moment(data.value, 'YYYYMMDD')
  />
  return wrapup(data, render)
}

const renderInput = (data, onChange) => {
  const valid = validate(data)
  return <Input
    disabled={data.disabled}
    addonAfter={
      data.addonAfter ? data.addonAfter :
        (data.addon && <Popover
          id={data.addon.id || "popover"}
          content={data.addon.detail}
          title={data.addon.title}
          trigger={data.addon.trigger || 'hover'}>
          <FontAwesomeIcon icon={data.addon.icon} />
        </Popover>)
    }
    type={data.password ? "password" : data.number ? "number" : 'text'}
    onChange={e =>
      onChange({ name: data.name, value: e.target.value, error: validate(data, e.target.value) })}
    name={data.name}
    className={valid === undefined ? '' : "has-error"}
    placeholder={data.placeholder}
    value={data.value}
    style={{ width: '100%', borderColor: valid === undefined ? '' : '#f5222d' }}
  />
}

const renderSelect = (data, onChange) => {
  const valid = validate(data)
  let isError = valid !== undefined
  if (data.isError) isError = data.isError
  const render = <DynamicDropdown
    btStyle={data.btStyle}
    isError={isError}
    selected={data.value}
    disabled={data.disabled}
    multiple={data.multiple}
    style={{ borderColor: isError ? '#f5222d' : '' }}
    onSelect={(item, selected) => onChange({ name: data.name, value: item, error: validate(data, item), selected })}
    placeholder={data.placeholder}
    options={data.options} />
  return wrapup(data, render)
}

const renderTextArea = (data, onChange) => {
  const valid = validate(data)
  return <TextArea
    className={valid === undefined ? '' : "has-error"}
    placeholder={data.placeholder}
    rows="3"
    name={data.name}
    disabled={data.disabled}
    onChange={e => onChange({ name: data.name, value: e.target.value, error: validate(data, e.target.value) })}
    value={data.value}
    style={{ width: '100%', borderColor: valid === undefined ? '' : '#f5222d' }}
  />
}

const renderRadioGroup = (data, onChange) => {
  const valid = validate(data)
  let isError = valid !== undefined
  if (data.isError) isError = data.isError
  const radioStyle = {
    display: 'block',
    height: '30px',
    lineHeight: '30px',
  }
  return <Radio.Group
    className={isError ? 'has-error' : ''}
    style={{ width: '100%', border: isError ? 'solid 1px #f5222d' : '' }}
    name={data.name}
    onChange={e => onChange({ name: data.name, value: e.target.value, error: validate(data, e.target.value) })}
    value={data.value}>
    {data.options.map((op, i) => op.description ?
      <div key={i} className="col-lg-12 col-md-12 col-sm-12 col-12 form-inline mb-2">
        <div className="col-lg-12 col-md-12 col-sm-12 col-12">
          <Radio value={op.value}>{op.text}</Radio>
        </div>
        <span className="col-auto ml-4">{op.description}</span>
      </div> :
      <Radio key={i} style={data.vertical && radioStyle} value={op.value}>{op.text}</Radio>
    )}
  </Radio.Group>
}

const renderRangePicker = (data, onChange) => {
  const valid = validate(data)
  const format = data.format || defaultFormat
  return <RangePicker
    className={valid === undefined ? '' : "has-error"}
    name={data.name}
    format={format}
    onChange={(value, dateString) =>
      onChange({ name: data.name, value: dateString, error: validate(data, dateString) })}
    locale={localeEN}
    value={data.value ? [
      data.value && data.value[0] !== "" ? moment(data.value[0]) : null,
      data.value && data.value[1] !== "" ? moment(data.value[1]) : null,
    ] : null}
    style={{ width: '100%' }}
  />
}

const renderCheckAll = (data, onChange) => {
  const valid = validate(data)
  return <div>
    <Checkbox
      name={data.name + 'all'}
      indeterminate={data.value && (data.value.length > 0 && data.value.length < data.options.length)}
      onChange={e => (
        onChange({ name: data.name, value: e.target.checked ? data.options.map(x => x.value) : [], error: valid })
      )}
      checked={data.value && data.value.length === data.options.length}
      className="d-inline-block mr-2 border-right">{data.label || 'All'}</Checkbox>
    <Checkbox.Group
      className={valid === undefined ? '' : "has-error"}
      name={data.name}
      options={data.options}
      onChange={checkedList => (
        onChange({ name: data.name, value: checkedList, error: valid })
      )}
      value={data.value || []} />
  </div>
}

const renderTimePicker = (data, onChange) => {
  const valid = validate(data)
  return <TimePicker
    className={valid === undefined ? '' : "has-error"}
    style={{ width: '100%' }}
    placeholder={''}
    disabled={data.disabled}
    value={data.value ? moment(data.value, data.format || 'HH:mm') : null}
    onChange={(time, timeString) =>
      onChange({ name: data.name, value: timeString, error: validate(data, timeString) })}
    format={data.format || 'HH:mm'} />
}

const renderCheckbox = (data, onChange) => {
  const valid = validate(data)
  let className = ''
  if (data.label) className = 'd-inline-block mr-2'
  if (valid !== undefined) className += ' has-error'
  return <Checkbox
    style={{ width: '100%' }}
    onChange={e =>
      onChange({ name: data.name, value: e.target.checked, error: validate(data, e.target.checked) })}
    checked={data.value}
    className={className}>{data.label || ''}</Checkbox>
}

const renderSwitch = (data, onChange) => {
  const valid = validate(data)
  let className = ''
  if (data.label) className = 'd-inline-block mr-2'
  if (valid !== undefined) className += ' has-error'
  return <Switch
    checked={data.value}
    onChange={checked => { onChange({ name: data.name, value: checked, error: validate(data, checked) }) }}
    className={className}>{data.label || ''}</Switch>
}

const renderSearch = (data, onChange) => {
  const valid = validate(data)
  const render = <Search
    disabled={data.disabled}
    addonBefore={
      data.addon && <Popover content={data.addon.detail} title={data.addon.title}>
        <FontAwesomeIcon color={data.addon.color} icon={data.addon.icon} />
      </Popover>
    }
    prefix={data.loading && <Icon type="loading" />}
    enterButton={data.enterButton}
    onChange={e => onChange({ name: data.name, value: e.target.value, error: validate(data, e.target.value) })}
    onSearch={data.onSearch}
    name={data.name}
    className={valid === undefined ? '' : "has-error"}
    placeholder={data.placeholder}
    value={data.value}
    style={{ width: '100%' }}
  />
  data.addon = null
  return wrapup(data, render)
}

export default (template, onChange) => {
  if (template.type === 'input') return renderInput(template, onChange)
  if (template.type === 'datepicker') return renderDatepicker(template, onChange)
  if (template.type === 'select') return renderSelect(template, onChange)
  if (template.type === 'textarea') return renderTextArea(template, onChange)
  if (template.type === 'radiogroup') return renderRadioGroup(template, onChange)
  if (template.type === 'rangepicker') return renderRangePicker(template, onChange)
  if (template.type === 'checkall') return renderCheckAll(template, onChange)
  if (template.type === 'timepicker') return renderTimePicker(template, onChange)
  if (template.type === 'checkbox') return renderCheckbox(template, onChange)
  if (template.type === 'switch') return renderSwitch(template, onChange)
  if (template.type === 'search') return renderSearch(template, onChange)
  return template.component
}
