import React, { Component } from 'react'
import { Table, Popover, message, Spin } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'
import { isReadOnly } from '../../utils/permission'
import { getListUserGroupPermission, delUserGroup } from '../../redux/actions/user'
import { getGroup } from '../../redux/actions/dropdown'

class UserDepartment extends Component {
  state = {
    data: [],
    isLoading: false,
    visible: {}
  }

  componentDidMount() {
    this.getList()
  }

  getList = () => {
    this.setState({ isLoading: true })
    this.props.getListUserGroupPermission().then(res => {
      this.setState({ isLoading: false })
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        const data = res.payload.data.data
        this.setState({
          data: data.map((d, i) => ({
            key: i,
            no: i + 1,
            id: d.userGroupId,
            userGroup: d.userGroupName,
            number: d.countUserGroupPermission,
            updateDate: d.dateUpdate
          }))
        })
      }
    })
  }

  add = () => {
    const { history } = this.props
    history.push('/department/addgroup')
  }

  view = userGroup => {
    const { history } = this.props
    if (userGroup) history.push('/department/viewuser/' + (userGroup || '0'))
    else history.push('/department/viewuser')
  }

  edit = id => {
    const { history } = this.props
    history.push('/department/editgroup/' + id)
  }

  delete = item => {
    let { visible } = this.state
    visible[item.key] = false
    this.setState({ visible })
    const body = {
      userGroupId: item.id
    }
    this.props.delUserGroup(body).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        message.success('Delete group ' + item.userGroup + ' success')
        this.getList()
        this.props.getGroup()
      }
    })
  }

  render() {
    const readOnly = isReadOnly('department')
    const { data, isLoading } = this.state

    const columns = [
      {
        title: '#',
        dataIndex: 'no',
        key: 'no',
        width: 50
      }, {
        title: 'User Group',
        dataIndex: 'userGroup',
        key: 'userGroup',
        align: 'left',
        width: 200
      }, {
        title: 'Number of User',
        dataIndex: 'number',
        key: 'number',
        width: 100
      }, {
        title: 'Update Date',
        dataIndex: 'updateDate',
        key: 'updateDate',
        width: 150
      }, {
        title: 'View User',
        key: 'view',
        width: 80,
        render: item => (
          <button onClick={() => this.view(item.id)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="file" />
          </button>
        )
      }, {
        title: 'Edit',
        key: 'edit',
        width: 80,
        render: item => (
          <button disabled={readOnly} onClick={() => this.edit(item.id)} type="button" className="btn btn-outline-warning btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: 'Delete',
        key: 'delete',
        width: 80,
        render: item => (
          item.number === 0 && (readOnly ? <button disabled type="button" className="btn btn-outline-danger btn-sm">
            <FontAwesomeIcon icon="trash" />
          </button> : <Popover
            visible={this.state.visible[item.key]}
            onVisibleChange={_visible => {
              let { visible } = this.state
              visible[item.key] = _visible
              this.setState({ visible })
            }}
            content={<button
              type="button"
              className="btn btn-outline-danger btn-sm"
              onClick={() => this.delete(item)}>Delete</button>}
            title="Are you sure?"
            trigger="click">
              <button type="button" className="btn btn-outline-danger btn-sm">
                <FontAwesomeIcon icon="trash" />
              </button>
            </Popover>)
        )
      },
    ]

    const table = <Table
      scroll={{ x: '1500px', y: 400 }}
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      footer={isLoading ? () => <div className="text-center"><Spin className="pr-2" />loading...</div> : null}
      dataSource={data} />

    return (
      <div className="card card-body card-body-component">
        <div className="card card-body card-header-table">
          {!readOnly && <div className="mb-3">
            <button type="button" onClick={this.add} className="btn btn-success">
              <FontAwesomeIcon icon="plus" /> Add Group
          </button>
            <button type="button" onClick={() => this.view()} className="btn btn-primary ml-2">
              <FontAwesomeIcon icon="file" /> View All User
          </button>
          </div>}
          {table}
        </div>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  getListUserGroupPermission: () => dispatch(getListUserGroupPermission()),
  delUserGroup: body => dispatch(delUserGroup(body)),
  getGroup: () => dispatch(getGroup())
})

export default connect(mapStateToProps, mapDispatchToProps)(UserDepartment)
