import React, { Component, Fragment } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Link } from 'react-router-dom'
import Render from '../../components/Renderer'
import Background from '../../images/img-login.png'
import { connect } from 'react-redux'
// import context from '../../utils/language'
import { dayEn, dayTh } from '../../utils/date'
import DatePickerExpand from '../../components/DatePickerExpand'
import FlightEditTable from './FlightEditTable'
import {
  getDetailFlightPlanSchedule,
  getListDetailFlightPlan,
  saveFlightPlanSchedule,
  updateFlightPlanSchedule
} from '../../redux/actions/plan'
import { general as gConstraints, table as tConstraints } from './Constraints'
import validatejs from 'validate.js'
import { message } from 'antd'
// import moment from 'moment'
import context from '../../utils/language'
import moment from 'moment'

const defaultDateValue = {
  year: [new Date().getFullYear()],
  month: [],
  days: [],
  isSaving: false,
  selectedFromTo: null
}

class FlightEdit extends Component {
  state = {
    form: {
      dateValue: defaultDateValue,
      dateType: 'range'
    },
    data: [{ key: 1, noHighlight: true }],
    error: null
  }

  componentDidMount() {
    const { match, permission } = this.props
    let form = {
      dateValue: defaultDateValue,
      dateType: 'range',
      airportFrom: permission.defaulfAirport
    }
    this.setState({ form }, () => {
      match.params.id && match.params.id !== '0' && this.props.getDetailFlightPlanSchedule(match.params.id).then(res => {
        if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
          const data = res.payload.data.data
          form = {
            flightPlanScheduleId: data.flightPlanScheduleId,
            traffType: data.traffType,
            dateList: data.dayFlightPlan ? data.dayFlightPlan.split(',').map(x => Number(x)) : [],
            dateType: data.typeDatePlan === 2 ? 'date' : 'range',
            dateValue: data.typeDatePlan === 2 ? {
              year: data.year.split(',').map(x => Number(x)),
              month: data.monthFlightPlan.split(',').map(x => Number(x)),
              days: data.dateFlightPlan.split(',').map(x => Number(x)),
            } : defaultDateValue,
            dateRange: data.typeDatePlan === 1 && [data.startDate, data.endDate],
            airportFrom: data.arrAirportId,
            airportTo: data.depAirportId,
            cardNo: data.cardNo
          }
          this.setState({ form })
        }
      })
    })
    match.params.id && match.params.id !== '0' && this.props.getListDetailFlightPlan(match.params.id).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        const data = res.payload.data.data
        let array = data.map((d, i) => ({
          key: i,
          flightNoPre: d.arrivalAirlineCode,
          flightNo: d && d.arrivalFlightCode.split(' ').pop(),
          depTime: d.arrivalTimeDeparture,
          arrTime: d.arrivalTimeArrival,
          lcd: d.arrStatusShow === 1,
          flightNo2Pre: d.departureAirlineCode,
          flightNo2: d && d.departureFlightCode.split(' ').pop(),
          depTime2: d.departureTimeDeparture,
          arrTime2: d.departureTimeArrival,
          lcd2: d.depStatusShow === 1,
          callsign: d.callsignId,
          airType: d.airtypeId,
          cardNo: d.cardNo,
          remark: d.remark,
          type: d.isIATAorIATA === 0 ? 'codeIATA' : 'codeICAO'
        }))
        array.push({ key: array.length + 1, noHighlight: true })
        this.setState({
          data: array
        })
      }
    })
  }

  onChange = ({ name, value, selected }) => {
    let { form } = this.state
    form[name] = value
    /* disable date before today
    const dateNow = moment().toDate().setHours(0, 0, 0, 0)
    if (name === 'dateRange') {
      if (moment(value[0]).toDate().setHours(0, 0, 0, 0) < dateNow) {
        return message.warning('Incorrect Date')
      }
    }
    */
    if (name === 'airportTo') return this.setState({ form, selectedFromTo: selected })
    this.setState({ form })
  }

  onBack = () => {
    const { history } = this.props
    history.goBack()
  }

  validate = item => {
    const valid = validatejs(item, tConstraints)
    return valid === undefined
  }

  onSave = () => {
    this.setState({ isSaving: true })
    const { match } = this.props
    let { form, data } = this.state
    data = data.filter(this.validate)
    let tmp = JSON.parse(JSON.stringify(gConstraints))
    if (form.dateType === 'date') {
      delete tmp.dateRange
      delete tmp.dateList
    }
    if (form.dateType === 'range') {
      delete tmp['dateValue.year']
      delete tmp['dateValue.month']
      delete tmp['dateValue.days']
    }
    const valid = validatejs(form, tmp)
    if (data.length === 0 || valid !== undefined) {
      let errors = []
      for (let key in valid) errors.push(valid[key])
      if (errors.length === 0) errors = ['No Detail']
      this.setState({ isSaving: false })
      return message.error('Error : ' + errors.join())
    }
    let body = {
      airportID: form.airportFrom || '',
      traffType: form.traffType || '',
      arrAirportId: form.airportFrom || '',
      depAirportId: form.airportTo || '',
      statusTypeDate: form.dateType === 'range' ? 1 : 2,
      listArrAirlineCode: data.map(x => x.flightNoPre).join(),
      listDepAirlineCode: data.map(x => x.flightNo2Pre).join(),
      listArrFlightCode: data.map(x => x.flightNo).join(),
      listDepFlightCode: data.map(x => x.flightNo2).join(),
      listArrEndTime: data.map(x => x.arrTime).join(),
      listArrStartTime: data.map(x => x.depTime).join(),
      listDepEndTime: data.map(x => x.arrTime2).join(),
      listDepStartTime: data.map(x => x.depTime2).join(),
      listArrShowLCD: data.map(x => x.lcd === true ? 1 : 0).join(),
      listDepShowLCD: data.map(x => x.lcd2 === true ? 1 : 0).join(),
      listCallsign: data.map(x => x.callsign).join(),
      listAirType: data.map(x => x.airType).join(),
      cardNo: form.cardNo || '',
      remark: form.remark || '',
      listFlagAirlineCode: data.map(x => x.type === 'codeIATA' ? 0 : 1).join(),
    }
    if (form.dateType === 'range') {
      body.dateStart = moment(form.dateRange[0]).format('YYYY-MM-DD')
      body.dateEnd = moment(form.dateRange[1]).format('YYYY-MM-DD')
      body.listDayFlightPlan = form.dateList.join()
    }
    if (form.dateType === 'date') {
      body.yearFlightPlan = form.dateValue.year.join()
      body.monthFlightPlan = form.dateValue.month.join()
      body.listDateFlightPlan = form.dateValue.days.join()
    }
    if (match.params.id && match.params.id !== '0') { // update
      body.flightPlanID = form.flightPlanScheduleId
      this.props.updateFlightPlanSchedule(body).then(res => {
        this.setState({ isSaving: false })
        if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
          message.success('Save success')
          this.onBack()
        }
      })
    } else {
      this.props.saveFlightPlanSchedule(body).then(res => {
        this.setState({ isSaving: false })
        if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
          message.success('Save success')
          this.onBack()
        }
      })
    }
  }

  render() {
    const { language } = this.props
    const name = context[language].flightEdit
    let { form, data, isSaving, selectedFromTo } = this.state
    const template = [{
      type: 'select',
      name: 'traffType',
      label: 'Traff Type',
      value: form.traffType,
      placeholder: '',
      constraint: gConstraints,
      options: {
        name: 'traffTypeOptions',
        id: 'traffTypeCode',
        text: x => x.traffTypeCode + ' - ' + x.traffTypeName
      }
    }, {
      type: 'radiogroup',
      name: 'dateType',
      label: 'Date',
      value: form.dateType,
      placeholder: '',
      options: [
        { text: 'Date Range', value: 'range' },
        { text: 'Specify Date', value: 'date' }
      ]
    }, {
      type: 'rangepicker',
      name: 'dateRange',
      language: language,
      format: 'DD MMM YYYY',
      constraint: form.dateType === 'range' && gConstraints,
      value: form.dateRange,
    }, {
      type: 'checkall',
      label: 'All',
      name: 'dateList',
      value: form.dateList || [],
      options: language === 'en' ?
        dayEn.map((x, i) => ({ label: x, value: i })) :
        dayTh.map((x, i) => ({ label: x, value: i }))
    }, {
      type: 'select',
      label: 'Airport',
      name: 'airportFrom',
      constraint: gConstraints,
      placeholder: 'Select...',
      value: form.airportFrom,
      options: {
        name: 'airportDOAOptions',
        id: 'airportID',
        text: x => x.airportName + ' (' + x.airportCodeICAO + ')'
      }
    }, {
      type: 'select',
      name: 'airportTo',
      placeholder: 'Select...',
      constraint: gConstraints,
      value: form.airportTo,
      options: {
        name: 'airPortOptions',
        id: 'airportID',
        text: x => x.airportName + ' (' + x.airportCodeICAO + ')'
      },
    }, {
      type: 'textarea',
      name: 'remark',
      label: 'Airport Remark',
      placeholder: 'Select...',
      constraint: gConstraints,
      value: form.remark,
    }, {
      type: 'input',
      name: 'cardNo',
      label: 'Card No.',
      placeholder: '',
      number: true,
      constraint: gConstraints,
      value: form.cardNo,
    }]
    return (
      <div className="card card-body card-body-component">
        <div className="row mb-2 form-inline">
          <img src={Background} alt={'lbg'} style={{
            position: 'absolute',
            top: 60,
            right: 20,
            opacity: 0.7,
            height: '35%',
          }} />
          <div className="col-md-3">
            <Link to="#" onClick={() => this.onBack()}>
              <FontAwesomeIcon icon="angle-double-left" /><span className="ml-2">Back</span>
            </Link>
          </div>

          <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-5 justify-content-end">{template[0].label} :</label>
            <div className="col-md-3 col-7">
              {Render(template[0], this.onChange)}
            </div>
          </div>

          <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-3 justify-content-end">{template[1].label} :</label>
            <div className="col-md-9 col-9">
              {Render(template[1], this.onChange)}
            </div>
          </div>

          {form.dateType === 'range' && <Fragment>
            <div className="col-12 col-lg-12 form-inline form-row mb-3">
              <div className="col-md-3 col-3" />
              <div className="col-md-4 col-7">
                {Render(template[2], this.onChange)}
              </div>
            </div>
            <div className="col-12 col-lg-12 form-inline form-row mb-3">
              <div className="col-md-3 col-3" />
              <div className="col-md-9 col-9">
                {Render(template[3], this.onChange)}
              </div>
            </div>
          </Fragment>}

          {form.dateType === 'date' && <div style={{ minWidth: 1200 }} className="col-12 col-lg-12 form-inline form-row mb-3">
            <div className="col-md-3 col-3" />
            <div className="col-md-9 col-12 ml-0">
              <DatePickerExpand name="dateValue" value={form.dateValue} onChange={this.onChange} />
            </div>

          </div>}

          <div className="col-12 col-lg-12 form-inline form-row">
            <label className="col-md-3 col-3 justify-content-end">{template[4].label} :</label>
            <div className="col-md-3 col-sm-4 col-12">
              {Render(template[4], this.onChange)}
            </div>
            <FontAwesomeIcon icon="exchange-alt" className="text-primary" />
            <div className="col-md-3 col-sm-4 col-12">
              {Render(template[5], this.onChange)}
            </div>
          </div>

          <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-3 justify-content-end"></label>
            <div className="col-md-3 col-sm-4 col-12" style={{ marginRight: 18 }}>
              {name && name.departmentOfAirports}
            </div>
            <div className="col-md-3 col-sm-4 col-12">
              {name && name.fromTo}
            </div>
          </div>

          {selectedFromTo && selectedFromTo.airportCodeICAO === 'ZZZZ' && selectedFromTo.airportCodeIATA === 'ZZZ' && <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-3 justify-content-end">{template[6].label} :</label>
            <div className="col-md-3 col-3">
              {Render(template[6], this.onChange)}
            </div>
          </div>}

          <div className="col-12 col-lg-12 form-inline form-row mb-3">
            <label className="col-md-3 col-6 justify-content-end">{template[7].label} :</label>
            <div className="col-md-3 col-sm-12 col-12">
              {Render(template[7], this.onChange)}
            </div>
          </div>

        </div>

        <FlightEditTable
          onChange={newForm => {
            for (let item of data) {
              const valid = validatejs(item, tConstraints)
              item.isError = valid !== undefined
            }
            data = JSON.parse(JSON.stringify(newForm))
            this.setState({ data })
          }}
          cardNo={form.cardNo}
          isSaving={isSaving}
          onBack={this.onBack}
          onSave={this.onSave}
          form={data}
          from={form.airportFrom}
          to={form.airportTo} />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
  dropdown: state.dropdown,
  permission: state.permission
})

const mapDispatchToProps = dispatch => ({
  getDetailFlightPlanSchedule: id => dispatch(getDetailFlightPlanSchedule(id)),
  getListDetailFlightPlan: id => dispatch(getListDetailFlightPlan(id)),
  saveFlightPlanSchedule: data => dispatch(saveFlightPlanSchedule(data)),
  updateFlightPlanSchedule: data => dispatch(updateFlightPlanSchedule(data)),
})

export default connect(mapStateToProps, mapDispatchToProps)(FlightEdit)
