var querystring = require('querystring')

const onError = ({ getState, dispatch, error }) => {
  let text = 'Request error'
  if (error && error.request && error.request.response) text = error.request.response.data
  dispatch({ type: 'REQUEST_ERROR', data: text, error });
}

export const getFlightStat = (param) => {
  return {
    type: 'GET_LIST_FLIGHT_STAT',
    payload: {
      request: {
        url: '/statistic/v1/GetListFlightStat?' + (param || '')
      },
      options: {
        onSuccess({ getState, dispatch, response }) {
          dispatch({ type: 'SET_LIST_FLIGHT_STAT', data: response.status === 200 ? response.data.data : [] });
        },
        onError
      }
    }
  }
}

export const getFlightStatById = (id) => {
  return {
    type: 'GET_A_FLIGHT_STAT',
    payload: {
      request: {
        url: '/statistic/v1/DataFlightStat?flightStatsID=' + (id || '')
      },
      options: {
        onSuccess({ getState, dispatch, response }) {
          dispatch({ type: 'SET_A_FLIGHT_STAT', data: response.data.data.dataFlightStat });
        },
        onError
      }
    }
  }
}

export const getFlightStatLogById = (id) => {
  return {
    type: 'GET_FLIGHT_STAT_LOG',
    payload: {
      request: {
        url: '/statistic/v1/GetListLogFlightStat?flightStatID=' + (id || '')
      },
      options: {
        onError
      }
    }
  }
}

export const updateFlightStatsStatus = (flightStatID, statusApprove) => {
  return {
    type: 'GET_FLIGHT_STATS_STATUS',
    payload: {
      request: {
        method: 'PUT',
        url: '/statistic/v1/UpdateFlightStatsStatus?flightStatID=' + flightStatID + '&statusApprove=' + statusApprove
      },
      options: {
        onError
      }
    }
  }
}

export const saveDetailFlightStat = data => {
  return {
    type: 'SAVE_DETAIL_FLIGHT_STAT',
    payload: {
      request: {
        method: 'POST',
        url: '/statistic/v1/SaveDetailFlightStat',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: querystring.stringify(data)
      },
      options: {
        onError
      }
    }
  }
}

