export const uploadFile = param => {
  var bodyFormData = new FormData()
  for (let key in param) bodyFormData.set(key, param[key])
  return {
    type: 'uploadFile',
    payload: {
      request: {
        method: 'POST',
        url: '/FilesUpload/v1/UploadFilesFlightStat',
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: bodyFormData
      },
      options: {
        onError
      }
    }
  }
}