import React, { Component } from 'react'
import { getDataUserAirport } from '../../redux/actions/user'
import { connect } from 'react-redux'
import { Button } from 'antd'

class SelectAirport extends Component {
  state = {
    data: {
      airportAccess: []
    }
  }

  componentDidMount() { // 1026
    this.props.getDataUserAirport('1002').then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        this.setState({ data: res.payload.data.data })
      }
    })
  }

  onClick = airport => {
    const { history } = this.props
    history.push('/selectairport/statistics/' + airport.airportID)
  }

  render() {
    const { data } = this.state
    const airports = this.props.dropdown.airportDOAOptions
    return (
      <div className="card card-body card-body-component">
        <div className="form-row form-inline mb-2">
          {data.airportAccess.map((a, i) => {
            const index = airports.findIndex(x => x.airportID === a)
            if (index === -1) return null
            const airport = airports[index]
            let color = 'bg-success'
            return <Button
              key={i}
              title={airport.airportName}
              onClick={() => this.onClick(airport)}
              className={"m-1 text-truncate text-white " + color}
              style={{ width: 150 }}>{airport.airportName}</Button>
          })}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
  dropdown: state.dropdown
})

const mapDispatchToProps = dispatch => ({
  getDataUserAirport: id => dispatch(getDataUserAirport(id))
})

export default connect(mapStateToProps, mapDispatchToProps)(SelectAirport)
