var querystring = require('querystring')

export const getListDetailFlightPlan = flightPlanId => {
  return {
    type: 'GET_LIST_DETAIL_FLIGHT_PLAN',
    payload: {
      request: {
        url: '/FlightPlan/v1/listDetailFlightPlanSchedule?flightPlanId=' + (flightPlanId || '')
      },
      options: {
        onError({ getState, dispatch, error }) {
          dispatch({ type: 'REQUEST_ERROR', data: 'request error listDetailFlightPlanSchedule', error });
        }
      }
    }
  }
}

