import React, { Component } from 'react'
import { Carousel as CS, Switch, Checkbox } from 'antd'
import './Carousel.css'

class Carousel extends Component {
  chunk = (array, chunk) => {
    if (!array) return []
    let temparray = []
    for (let i = 0, j = array.length; i < j; i += chunk) {
      temparray.push(array.slice(i, i + chunk))
    }
    return temparray
  }

  onChange = (item, checked) => {
    let { data } = this.props
    const index = data.findIndex(x => x.id === item.id)
    if (index > -1) {
      data[index].checked = checked
      this.props.onChange(data)
    }
  }

  onCheckAll = e => {
    let { data } = this.props
    for (let d of data) d.checked = e.target.checked
    this.props.onChange(data)
  }

  render() {
    const { data } = this.props
    const array = this.chunk(data, 8)
    const countCheck = data.filter(x => x.checked).length
    return (
      <div className=" mb-4 col-sm-12">
        <div className="form-row">
          <Checkbox
            checked={countCheck === data.length}
            indeterminate={countCheck > 0 && countCheck < data.length}
            onChange={this.onCheckAll}>
          </Checkbox>
          <span className="ml-2">All</span>
        </div>
        <CS>
          {array.map((c, i) => (
            <div key={i} className="mb-2">
              <div className="form-row form-inline">
                {c.map((d, i) => (
                  <div key={i} className="col-6 col-sm-3 mb-3 text-right">
                    <span className="mr-2">{d.text}</span>
                    <Switch
                      checked={d.checked}
                      onChange={checked => this.onChange(d, checked)}
                      style={{ backgroundColor: d.checked ? d.color : '#b1b2b1' }} />
                  </div>
                ))}
              </div>
            </div>
          ))}
        </CS>
      </div>
    );
  }
}

export default Carousel;