import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { isReadOnly } from '../../../utils/permission'
import { Table, message } from 'antd'
import Render from '../../../components/Renderer'
import { insertManageRelation, updateManageRelation } from '../../../redux/actions/setting'
import { connect } from 'react-redux'
import { Constraints } from './Constraints'
import validatejs from 'validate.js'

class RelationEdit extends Component {
  state = {
    form: {},
    data: [{ key: 1 }],
    disabledSave: false
  }

  componentDidMount() {
    const { selected } = this.props
    let { form } = this.state
    form = { ...selected, ...form }
    let data = []
    if (selected && selected.list) data = selected.list
    data.forEach((x, i) => x.key = i)
    data.push({ key: data.length + 1 })
    this.setState({ form, errors: validatejs(form, Constraints), data })
  }

  onChange = ({ name, value, error }) => {
    let { form, errors } = this.state
    form[name] = value
    if (errors === undefined) errors = {}
    if (error === undefined) delete errors[name]
    else errors[name] = error.join()
    if (Object.keys(errors).length === 0) errors = undefined
    this.setState({ form, errors })
  }

  onDelete = index => {
    let { data } = this.state
    data.splice(index, 1)
    this.setState({ data })
  }

  onAdd = () => {
    let { data } = this.state
    for (let [i, d] of data.entries()) d.key = i + 1
    data.push({ key: data.length + 1 })
    this.setState({ data })
  }

  onChangeTable = (index, name, value) => {
    let { data } = this.state
    data[index][name] = value
    this.setState({ data })
  }

  renderSelect = (name, row, index) => {
    const template = {
      type: 'select',
      name: name,
      value: row[name],
      placeholder: '',
      options: {
        name: 'airTypeOptions',
        id: 'airType',
        text: 'airType',
      }
    }
    return Render(template, ({ name, value, selected }) => this.onChangeTable(index, name, value))
  }

  renderGrossweight = (name, row, index) => {
    const template = {
      type: 'select',
      name: name,
      value: row[name],
      placeholder: '',
      options: {
        name: 'airTypeOptions',
        id: 'airTypeID',
        text: x => x.grossWeight + ' kg. / ' + x.pax + ' pax',
        preFilter: x => x.airType === row.airtypeCode,
        filter: (thing, index, self) => index === self.findIndex((t) => (t.id === thing.id))
      }
    }
    return Render(template, ({ name, value }) => this.onChangeTable(index, name, value))
  }

  onSave = () => {
    this.setState({ disabledSave: true }, () => {
      const { selected } = this.props
      const { form, data } = this.state
      let body = {
        "airlineId": form.airlineId,
        "callsignId": form.callsignId,
        "ownerType": form.ownerType,
        "ownerId": form.ownerId,
        "ownerOther": form.ownerOther
      }
      body.listAirtype = data.filter(x => x.airtypeId !== undefined).map(x => ({ airtypeId: x.airtypeId }))

      if (!selected) {
        this.props.insertManageRelation(body).then(res => {
          if (res && res.payload.status === 201 && res.type.endsWith('SUCCESS')) {
            body.id = res.payload.data.data.id
            message.success('Insert Relatione success')
            this.props.onRefresh(this.props.name, body)
            setTimeout(() => { this.setState({ disabledSave: false }) }, 500)
          }
        })
      } else {
        this.props.updateManageRelation(body).then(res => {
          if (res && res.payload.status === 201 && res.type.endsWith('SUCCESS')) {
            message.success('Update Relatione success')
            this.props.onRefresh(this.props.name, body)
            setTimeout(() => { this.setState({ disabledSave: false }) }, 500)
          }
        })
      }
    })
  }

  render() {
    const { form, data, errors, disabledSave } = this.state
    const readOnly = isReadOnly('relationsetting')
    const template = [{
      type: 'select',
      name: 'airlineId',
      label: 'Airline',
      disabled: readOnly,
      constraint: Constraints,
      value: form.airlineId,
      options: {
        name: 'airlineOptions',
        id: 'airlineID',
        text: 'showFilter'
      },
      button: <button type="button" onClick={() => this.props.onEdit('isAirlineEdit')} className="btn btn-sm btn-success">
        <FontAwesomeIcon icon="plus" /> Add Airline
      </button>
    }, {
      type: 'description',
      description: 'if you don\'t find desired airline. Please click "Add Airline"',
    }, {
      type: 'select',
      name: 'callsignId',
      label: 'Callsign',
      constraint: Constraints,
      value: form.callsignId,
      placeholder: 'Select Callsign...',
      options: {
        name: 'callsignOptions',
        id: 'callsignID',
        text: 'callsignCode'
      },
      button: <button type="button" onClick={() => this.props.onEdit('isCallsignEdit')} className="btn btn-sm btn-success">
        <FontAwesomeIcon icon="plus" /> Add Callsign
      </button>
    }, {
      type: 'description',
      description: 'if you don\'t find desired callsign. Please click "Add Callsign"',
    }, {
      type: 'radiogroup',
      name: 'ownerType',
      label: 'Owner',
      constraint: Constraints,
      value: form.ownerType,
      placeholder: '',
      options: [
        { text: 'Airline', value: 1 },
        { text: 'Other', value: 2 },
        { text: 'Not Specify', value: 3 }
      ]
    }]

    const airlineTemplate = [{
      type: 'select',
      name: 'ownerId',
      value: form.ownerId,
      placeholder: 'Select...',
      options: {
        name: 'airlineOptions',
        id: 'airlineID',
        text: 'airlineCompany'
      }
    }, {
      type: 'input',
      name: 'ownerOther',
      value: form.ownerOther,
      placeholder: '',
    }]

    const columns = [
      {
        title: 'Air Type',
        key: 'airtypeCode',
        width: 150,
        render: (text, row, index) => this.renderSelect('airtypeCode', row, index)
      }, {
        title: 'Gross Weight (kg.) / Pax',
        width: 150,
        render: (text, row, index) => this.renderGrossweight('airtypeId', row, index)
      }, {
        title: 'Edit',
        key: 'edit',
        width: 80,
        render: (text, row, index) => <button
          onClick={() => {
            index + 1 === data.length ? this.onAdd(index) : this.onDelete(index)
          }}
          type="button"
          className={"btn btn-sm " + (index + 1 === data.length ? 'btn-outline-success' : 'btn-outline-danger')}>
          <FontAwesomeIcon icon={index + 1 === data.length ? "plus" : "trash"} />
        </button>
      }
    ]
    //const sum = columns.reduce((a, b) => a + b.width, 0)
    const table = <Table
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      dataSource={data} />

    return (
      <div className="card card-body card-body-component p-0">
        <div className="card card-body card-header-table">
          <button type="button" onClick={() => this.props.onClose(this.props.name)} className="btn btn-sm btn-success" style={{ width: 100 }}>
            <FontAwesomeIcon icon="angle-double-left" />
            <span className="ml-2">Back</span>
          </button>
          {template.map((data, i) => {
            if (data.type === 'description') return <div key={i} className="form-row form-inline mb-2 col-12 px-0">
              <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end mr-1"></label>
              {data.description}
            </div>
            return <div key={i} className="form-row form-inline mb-2 col-12 px-0">
              <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">{data.label} :</label>
              <div className="col-lg-4 col-md-6 col-sm-8 col-12">
                {Render(data, this.onChange)}
              </div>
              {data.button && <div className="col-lg-2 col-md-3 col-sm-4 col-5">
                {data.button}
              </div>}
            </div>
          })}
          {form.ownerType === 1 && <div className="form-row form-inline mb-2 col-12 px-0">
            <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end"></label>
            <div className="col-lg-4 col-md-6 col-sm-8 col-12">
              {Render(airlineTemplate[0], this.onChange)}
            </div>
          </div>}
          {form.ownerType === 2 && <div className="form-row form-inline mb-2 col-12 px-0">
            <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end"></label>
            <div className="col-lg-4 col-md-6 col-sm-8 col-12">
              {Render(airlineTemplate[1], this.onChange)}
            </div>
          </div>}

          <button type="button" onClick={() => this.props.onEdit('isAirTypeEdit')} className="btn btn-sm btn-success" style={{ width: 180 }}>
            <FontAwesomeIcon icon="plus" /> Manage Air Type
          </button>
          <span className="my-1">if you don't find desired Air Type or Gross Weight Please click "Manage Air Type"</span>

          {table}

          {/* footer */}
          <div className="text-center mt-2 pb-5">
            <button
              disabled={errors !== undefined || disabledSave}
              onClick={this.onSave}
              className="btn btn-success mr-2 mb-5">
              Save
          </button>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  insertManageRelation: body => dispatch(insertManageRelation(body)),
  updateManageRelation: body => dispatch(updateManageRelation(body)),
})

export default connect(mapStateToProps, mapDispatchToProps)(RelationEdit)
