import React, { Component } from 'react'
import { Modal, Divider, Button } from 'antd'
import StatTable from './StatTable'
import { exportExcel } from '../../utils/excel'
import Render from '../../components/Renderer'
import { connect } from 'react-redux'
import { getReport } from '../../redux/actions/report'
import moment from 'moment'

class OperationReport extends Component {
  state = {
    form: {},
    isOpenModal: false,
    selected: {},
    isLoading: false
  }

  numberWithCommas = (x) => {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  }

  exportExcel = data => {
    const table = `<table>
      ${data.map(t => {
      const title = t.title.map(r => `<tr>
        <th rowSpan='${r.rowspan || 1}' colSpan='${r.colspan || 1}' style='text-align:${r.align || 'center'}'>${r.text}</th>
      </tr>`).join('')

      const headers = t.headers.map(r => `<tr>
          ${r.map(c => `<th style='background-color: #d7e4f2; border: solid 1px #000; text-align:${r.align || 'center'}' 
          rowSpan='${c.rowspan || 1}' 
          colSpan='${c.colspan || 1}'>${c.text}</th>`).join('')}
        </tr>`).join('')

      const body = t.body.map(r => `<tr>
        ${r.map(c => `<th style='border: solid 1px #000; text-align:${typeof (c.text) === 'number' ? 'right' : (r.align || 'center')}' 
        rowSpan='${c.rowspan || 1}' 
        colSpan='${c.colspan || 1}'>${typeof (c.text) === 'number' ? this.numberWithCommas(c.text) : c.text}</th>`).join('')}
      </tr>`).join('')

      const margin = '<tr /><tr /><tr />'
      return title + headers + body + margin
    }
    ).join('')}
    </table>`
    exportExcel('report-seq-' + this.state.selected.seq, table)
    this.toggleModal()
  }

  onSelect = selected => {
    this.setState({ selected, form: {} })
    this.toggleModal()
  }

  toggleModal = () => this.setState({ isOpenModal: !this.state.isOpenModal })

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form })
  }

  getParams = () => {
    const { form } = this.state
    let params = ''
    Object.keys(form).forEach(key => {
      params += '&' + key + '=' + (key === 'dateRange' ? moment(form[key]).format('YYYY-MM-DD') : form[key])
    })
    return params
  }

  onDownload = () => {
    const { selected } = this.state
    this.setState({ isLoading: true })
    //const param = 'airportID=14&startDate=20190101&endDate=20190331'
    this.props.getReport(selected.seq, this.getParams()).then(res => {
      this.setState({ isLoading: false })
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        const data = res.payload.data.data
        this.exportExcel(data)
      }
    })
  }

  validate = () => {
    let { selected } = this.state
    let valid = true
    if (!selected.filter) selected.filter = []
    selected.filter.forEach(x => {
      if (!x.value || x.value === '' || x.value === []) valid = false
    })
    return valid
  }

  render() {
    const { isOpenModal, form, isLoading, selected } = this.state
    if (!selected.filter) selected.filter = []
    const template = selected.filter && selected.filter.map(x => {
      x.value = form[x.name]
      return x
    })
    return (
      <div className="card card-body card-body-component">
        <StatTable onSelect={this.onSelect} />

        <Modal
          title={<div>
            <h4>Export</h4>
            <div>
              <h5>{selected.name}</h5>
            </div>
          </div>}
          visible={isOpenModal}
          onCancel={this.toggleModal}
          destroyOnClose={true}
          footer={null}>

          <div className="form-row">
            {template.map((data, i) => <div key={i} className="col-12 col-md-12 col-xl-12 col-lg-12 form-inline form-row mb-2">
              <label className="col-6 col-md-4 col-xl-3 col-lg-4 text-right justify-content-end">{data.label} :</label>
              <div className="col col-xl-7">
                {Render(data, this.onChange)}
              </div>
            </div>)}
          </div>

          <Divider />

          {/* footer */}
          <div className="text-center mt-2">
            <Button
              onClick={this.toggleModal}
              className="btn-light mr-2">
              Cancel
            </Button>
            <Button
              disabled={!this.validate()}
              loading={isLoading}
              onClick={this.onDownload}
              className="btn-success mr-2">
              Download
          </Button>
          </div>
        </Modal>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
})

const mapDispatchToProps = dispatch => ({
  getReport: (seq, param) => dispatch(getReport(seq, param)),
})

export default connect(mapStateToProps, mapDispatchToProps)(OperationReport)
