import React, { Component } from 'react'
import TableComponent from './TableComponent'
import SearchComponent from './SearchComponent'
import { getFlightStat } from '../../redux/actions/flight'
import { connect } from 'react-redux'
import moment from 'moment'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

const dateFormat = 'DD MMM YYYY'
class Statistics extends Component {
  state = {
    form: {
      flightDate: moment().format(dateFormat)
    },
    isLoading: false
  }

  componentDidMount() {
    const { match } = this.props
    if (match.params.id) {
      this.setState({
        form: {
          flightDate: moment().format(dateFormat),
          airportID: Number(match.params.id)
        }
      }, this.onSearch)
    } else {
      this.onSearch()
    }
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    if (name === 'trafftype') value.sort()
    form[name] = value
    if (value === "") delete form[name]
    if (name === 'range') {
      if (value.join('').length === 0) delete form[name]
    }
    this.setState({ form: JSON.parse(JSON.stringify(form)) }, () => {
      if (Object.keys(form).length === 0) this.onSearch()
    })
  }

  onSearch = () => {
    this.setState({ isLoading: true })
    let { form } = this.state
    const { permission } = this.props
    if (!form.airportID) form.airportID = permission.defaulfAirport
    if (Object.keys(form).length === 0) form.flightDate = moment().format('YYYY-MM-DD')
    let DA = '-'
    if (form.checkInOut) {
      if (form.checkInOut.indexOf('1') > -1) DA = 'A'
      if (form.checkInOut.indexOf('2') > -1) DA = 'D'
      if (form.checkInOut.length === 2) DA = '-'
    }
    let trange = ''
    if (form.startTime) trange = form.startTime
    if (form.endTime) form.startTime ? (trange += ',' + form.endTime) : trange = form.endTime
    let param = ''
    let isDateRang = false, isAirportList = false
    if (form.range) if (form.range.join('').length > 0) isDateRang = true
    if (form.destination) if (form.destination.join('').length > 0) isAirportList = true
    if (Object.keys(form).length > 0) {
      if (!isDateRang) param += '&flightDate=' + (moment(form.flightDate).format('YYYY-MM-DD') || moment().format('YYYY-MM-DD'))
      if (!isAirportList) param += '&airportID=' + (form.airportID || permission.defaulfAirport)
      param += '&airlineCodeIATA=' + (form.airlineCodeIATA || '-')
      param += '&flightCode=' + (form.flightNo || '-')
      if (isDateRang) param += '&dateRange=' + [moment(form.range[0]).format('YYYY-MM-DD'), moment(form.range[1]).format('YYYY-MM-DD')].join()
      param += '&timeRange=' + trange
      param += '&listCallsign=' + (form.callsign ? form.callsign.join() : '0')
      param += '&listAirType=' + (form.airTypeID || '0')
      param += '&listTraffType=' + (form.trafftype ? form.trafftype.join() : '0')
      if (isAirportList) param += '&listAirPort=' + form.destination.join()
      param += '&DepartureArrivalStatus=' + DA
      param += '&cardNo=' + (form.cardNo || '999')
      param += '&flightStatusApproveID=' + (form.flightStatus || '0')
    }
    this.props.getFlightStat(param).then(res => this.setState({ isLoading: false }))
  }

  onReset = () => this.setState({ form: { flightDate: moment().format(dateFormat) } }, () => this.onSearch())

  addDate = plus => {
    let { form } = this.state
    form.flightDate = moment(form.flightDate, dateFormat).add(plus, "days").format(dateFormat)
    this.setState({ form: JSON.parse(JSON.stringify(form)) }, () => this.onSearch())
  }

  onToday = () => {
    let { form } = this.state
    form.flightDate = moment().format(dateFormat)
    delete form.range
    this.setState({ form: JSON.parse(JSON.stringify(form)) }, () => this.onSearch())
  }

  onChangeDate = dateString => {
    let { form } = this.state
    form.flightDate = moment(dateString).format(dateFormat)
    delete form.range
    this.setState({ form: JSON.parse(JSON.stringify(form)) }, () => this.onSearch())
  }

  onBack = () => {
    const { history } = this.props
    history.goBack()
  }

  render() {
    const { permission, match } = this.props
    let { form, isLoading } = this.state
    if (!form.airportID) form.airportID = permission.defaulfAirport
    return (
      <div className="card card-body card-body-component">
        {match.params.id !== undefined && <button type="button"
          onClick={this.onBack}
          className="btn btn-sm btn-success mb-3" style={{ width: 100 }}>
          <FontAwesomeIcon icon="angle-double-left" />
          <span className="ml-2">Back</span>
        </button>}
        <SearchComponent
          form={form}
          isDisableAirport={match.params.id !== undefined}
          onChange={this.onChange}
          onSearch={this.onSearch}
          onReset={this.onReset} />
        <TableComponent
          form={form}
          isLoading={isLoading}
          refresh={this.onSearch}
          onPrevDate={() => this.addDate(-1)}
          onNextDate={() => this.addDate(1)}
          onToday={this.onToday}
          onChangeDate={this.onChangeDate}
        />
      </div>
    )
  }
}

const mapStateToProps = state => ({
  language: state.language,
  permission: state.permission
})

const mapDispatchToProps = dispatch => ({
  getFlightStat: (param) => dispatch(getFlightStat(param))
})

export default connect(mapStateToProps, mapDispatchToProps)(Statistics)
