import React, { Component } from 'react'
import { Tabs, Table, Spin } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import './ResultTable.css'
import ModalForm from '../Statistics/ModalComponent'
import { isReadOnly } from '../../utils/permission'
import { connect } from 'react-redux'
import { getFlightStatById } from '../../redux/actions/flight'

const TabPane = Tabs.TabPane

class ResultTable extends Component {
  state = {
    index: '1',
    isShowModal: false
  }

  onChangeTab = (key) => this.setState({ index: key }, () => this.props.onTabChange(key))

  onUpdateStatus = item => {
    const { index } = this.state
    item.type = index === '1' ? 'arrival' : 'departure'
    this.props.onUpdateStatus(item)
  }

  edit = item => {
    const { history } = this.props
    history.push('/flightschedule/edit/' + item.flightPlanScheduleId)
  }

  view = item => {
    this.props.getFlightStatById(item.flightStatsID)
    this.setState({ isShowModal: true })
  }

  onClose = () => this.setState({ isShowModal: false })

  render() {
    const { index, isShowModal } = this.state
    const { data, isLoading } = this.props
    const readOnly = isReadOnly('flightdetail')
    const bgColor = index === '1' ? 'bg-warning text-white' : 'bg-success text-white'
    let columns = [
      {
        title: 'Time', dataIndex: index === '1' ? 'arrivalTime' : 'departureTime', onHeaderCell: () => ({ className: bgColor })
      },
      { title: 'Flight No.', dataIndex: 'flightNo', onHeaderCell: () => ({ className: bgColor }) },
      {
        title: index === '1' ? 'From' : 'To',
        dataIndex: index === '1' ? 'arrivalAirportName' : 'departureAirportName',
        onHeaderCell: () => ({ className: bgColor })
      },
      { title: 'Code share', dataIndex: 'codeShare', onHeaderCell: () => ({ className: bgColor }) },
      { title: 'Terminal', dataIndex: 'terminal', onHeaderCell: () => ({ className: bgColor }) },
      index === '2' && { title: 'Gate', dataIndex: 'gateName', onHeaderCell: () => ({ className: bgColor }) },
      index === '1' && { title: 'Belt', dataIndex: 'beltName', onHeaderCell: () => ({ className: bgColor }) },
      {
        title: 'LCD',
        render: item => <FontAwesomeIcon className={"fa-lg " + (item.showLCD === 1 && 'text-primary')} icon={item.showLCD === 1 ? 'eye' : 'eye-slash'} />,
        onHeaderCell: () => ({ className: bgColor })
      }, {
        title: 'Flight Status',
        align: 'center',
        render: item => <button
          disabled={readOnly}
          onClick={() => this.onUpdateStatus(item)}
          className={item.flightStatusId === 0 ? "btn btn-primary btn-sm" : "btn btn-sm"}>
          {item.flightStatusId === 0 && <FontAwesomeIcon icon="clock" className="mr-2" />}
          {item.buttonText}
        </button>,
        onHeaderCell: () => ({ className: bgColor })
      }, {
        title: 'Edit',
        onHeaderCell: () => ({ className: bgColor }),
        render: item => <button onClick={() => this.edit(item)} disabled={readOnly} className="btn btn-outline-warning btn-sm">
          <FontAwesomeIcon icon="pen" />
        </button>
      }, {
        title: 'Manage Stat Data',
        onHeaderCell: () => ({ className: bgColor }),
        render: item => <button onClick={() => this.view(item)} disabled={readOnly} className="btn btn-outline-success btn-sm">
          <FontAwesomeIcon icon="plane" />
        </button>
      }
    ]
    columns = columns.filter(x => x !== false)

    let dataTable = index === '1' ? (data && data.arrivalList) : (data && data.departureList)
    if (!dataTable) dataTable = []
    dataTable.forEach((x, i) => x.key = i)
    dataTable.sort((a, b) => {
      if (a.arrivalTime && b.arrivalTime) {
        return a.arrivalTime.localeCompare(b.arrivalTime)
      }
      if (a.departureTime && b.departureTime) {
        return a.departureTime.localeCompare(b.departureTime)
      }
      return -1
    })
    const table = <Table
      footer={isLoading ? () => <div className="text-center"><Spin className="pr-2" />loading...</div> : null}
      className="tbody-center bg-white"
      columns={columns}
      scroll={{ x: '1500px' }}
      pagination={{
        defaultPageSize: 10,
        position: 'bottom'
      }}
      dataSource={dataTable} />

    return (
      <div className="card card-body card-header-table">
        <Tabs defaultActiveKey={index} onChange={this.onChangeTab} className="tabs-button">
          <TabPane tab={<span><FontAwesomeIcon icon="plane-arrival" /> Arrival</span>}
            key="1">
            {table}
          </TabPane>
          <TabPane tab={<span>Departure <FontAwesomeIcon icon="plane-departure" /></span>}
            key="2">
            {table}
          </TabPane>
        </Tabs>
        <ModalForm
          visible={isShowModal}
          onClose={this.onClose}
          refresh={this.props.onRefresh}
        />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
})

const mapDispatchToProps = dispatch => ({
  getFlightStatById: (id) => dispatch(getFlightStatById(id)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ResultTable)
