const empty = {
  presence: {
    allowEmpty: false,
    message: 'require'
  }
}

const numberEng = function (value, attributes, attributeName, options, constraints) {
  if (/^[A-Za-z][A-Za-z0-9]*$/.test(value)) return undefined;
  return {
    presence: {
      allowEmpty: false,
      message: "is required with a-z A-Z 0-9"
    }
  }
}

