var querystring = require('querystring')

const options = {
  onError({ getState, dispatch, error }) {
    let text = 'Request error'
    if (error && error.request && error.request.response) text = error.request.response.data
    dispatch({ type: 'REQUEST_ERROR', data: text, error });
  }
}

export const getListUserGroupPermission = () => {
  return {
    type: 'ListUserGroupPermission',
    payload: {
      request: {
        url: '/usermanagement/v1/ListUserGroupPermission'
      },
      options: options
    }
  }
}

export const getUserGroupData = id => {
  return {
    type: 'GetUserGroupData',
    payload: {
      request: {
        url: '/usermanagement/v1/GetUserGroupData?userGroupId=' + id
      },
      options: options
    }
  }
}

export const insertUserGroup = body => {
  return {
    type: 'insertUserGroup',
    payload: {
      request: {
        method: 'POST',
        url: '/usermanagement/v1/insertUserGroup',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        data: querystring.stringify(body)
      },
      options: options
    }
  }
}

