export const uploadFile = param => {
  var bodyFormData = new FormData()
  for (let key in param) bodyFormData.set(key, param[key])
  return {
    type: 'uploadFile',
    payload: {
      request: {
        method: 'POST',
        url: '/Setting/v1/UploadFiles',
        headers: {
          'Content-Type': 'multipart/form-data'
        },
        data: bodyFormData
      },
      options: options
    }
  }
}

