import React, { Component } from 'react'
import Render from '../../../components/Renderer'
import { Table, Divider, Switch, message } from 'antd'
import { isReadOnly } from '../../../utils/permission'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import validatejs from 'validate.js'
import { table as tConstraints, Constraints } from './Constraints'
import { connect } from 'react-redux'
import { getTerminal } from '../../../redux/actions/dropdown'
import { insertTerminal, deleteTerminal, getListSettingTerminal, updateStatusTerminal } from '../../../redux/actions/setting'

class TerminalAdd extends Component {
  state = {
    form: {},
    data: [],
    disabledSave: false
  }

  componentDidMount() {
    const data = [{ key: 1, isError: true }]
    this.setState({ data, errors: validatejs({}, Constraints) })
  }

  search = () => {
    let { form } = this.state
    let param = ''
    if (form.airportId !== undefined) {
      param = 'airportId=' + form.airportId
      this.getList(param)
    }
  }

  getList = (param) => {
    this.props.getListSettingTerminal(param).then(res => {
      this.setState({ isLoading: false })
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        let data = res.payload.data.data
        data.push({})
        data.forEach((x, i) => {
          x.key = i
          x.display = x.statusDisplay === 1
        })
        this.setState({ data })
      }
    })
  }

  onDelete = (item, index) => {
    let { data } = this.state
    data.splice(index, 1)
    this.setState({ data })
    if (item.terminalId) this.onDeleteOnline(item)
  }

  onAdd = () => {
    let { data } = this.state
    data.push({ key: data.length + 1 })
    this.setState({ data })
  }

  onChange = ({ name, value, error }) => {
    let { form, errors } = this.state
    form[name] = value
    if (errors === undefined) errors = {}
    if (error === undefined) delete errors[name]
    else errors[name] = error.join()
    if (Object.keys(errors).length === 0) errors = undefined
    this.setState({ form, errors }, this.search)
  }

  onChangeTable = (index, name, value) => {
    let { data } = this.state
    data[index][name] = value
    for (let item of data) {
      const valid = validatejs(item, tConstraints)
      item.isError = valid !== undefined
    }
    this.setState({ data })
  }

  renderInput = (name, row, index) => {
    var template = {
      type: 'input',
      name: name,
      value: row[name],
      disabled: row.terminalId !== undefined,
      placeholder: '',
    }
    return Render(template, ({ name, value }) => this.onChangeTable(index, name, value))
  }

  onDeleteOnline = item => {
    this.props.deleteTerminal(item.terminalId).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        message.success('Delete Terminal success')
        this.props.getTerminal()
      }
    })
  }

  onSave = () => {
    this.setState({ disabledSave: true }, () => {
      const { form, data } = this.state
      const clean = data.filter(x => !x.isError && x.terminalId === undefined)
      const body = clean.map(x => ({
        "airportId": form.airportId,
        "terminalCode": x.terminalCode,
        "terminalNameTH": x.terminalNameTh,
        "terminalNameEN": x.terminalNameEn,
        "remark": x.remark,
        "isEnable": (x.display ? 1 : 0),
      }))
      this.props.insertTerminal(body).then(res => {
        setTimeout(() => { this.setState({ disabledSave: false }) }, 500)
        if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
          message.success('Insert Terminal success')
          this.props.getTerminal()
          this.props.onClose(this.props.name)
        }
      })
    })
  }

  edit = (item) => this.props.onEdit(this.props.name, item)

  refresh = () => {
    this.props.onRefreshDone()
    setTimeout(() => { this.search() }, 200)
  }

  onCheckChange = (item, checked, index) => {
    if (item.terminalId) {
      const body = {
        terminalId: item.terminalId,
        statusDisplay: checked ? 1 : 0
      }
      this.props.updateStatusTerminal(body).then(res => {
        if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
          message.success('Update status success')
          this.search(true)
        }
      })
    } else {
      let { data } = this.state
      data[index].display = checked
      this.setState({ data })
    }
  }

  render() {
    const { form, data, errors, disabledSave } = this.state
    const { refresh } = this.props
    if (refresh) this.refresh()
    const readOnly = isReadOnly('airtypesetting')
    const template = [{
      type: 'select',
      name: 'airportId',
      label: 'Airport',
      constraint: Constraints,
      value: form.airportId,
      placeholder: 'Select Airport...',
      options: {
        name: 'airportDOAOptions',
        id: 'airportID',
        text: 'airportName'
      }
    }]
    const columns = [
      {
        title: 'Terminal (ENG)',
        key: 'terminalNameEn',
        dataIndex: 'terminalNameEn',
        width: 150,
        render: (text, row, index) => this.renderInput('terminalNameEn', row, index)
      }, {
        title: 'Terminal (TH)',
        dataIndex: 'terminalNameTh',
        key: 'terminalNameTh',
        width: 150,
        render: (text, row, index) => this.renderInput('terminalNameTh', row, index)
      }, {
        title: 'Code',
        dataIndex: 'terminalCode',
        key: 'terminalCode',
        width: 150,
        render: (text, row, index) => this.renderInput('terminalCode', row, index)
      }, {
        title: 'Remark',
        dataIndex: 'remark',
        key: 'remark',
        width: 150,
        render: (text, row, index) => this.renderInput('remark', row, index)
      }, {
        title: 'Display (On/Off)',
        key: 'display',
        width: 150,
        render: (text, row, index) => <Switch
          disabled={readOnly}
          onChange={checked => this.onCheckChange(row, checked, index)}
          checked={row.display} />
      }, {
        title: 'Edit',
        key: 'edit',
        width: 80,
        render: item => (
          <button disabled={readOnly} onClick={() => this.edit(item)} type="button" className="btn btn-outline-warning btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: '',
        key: 'add',
        width: 80,
        render: (text, row, index) => <button
          onClick={() => {
            index + 1 === data.length ? this.onAdd(index) : this.onDelete(row, index)
          }}
          type="button"
          className={"btn btn-sm " + (index + 1 === data.length ? 'btn-outline-success' : 'btn-outline-danger')}>
          <FontAwesomeIcon icon={index + 1 === data.length ? "plus" : "trash"} />
        </button>
      }
    ]
    const sum = columns.reduce((a, b) => a + b.width, 0)
    const table = <Table
      style={{ minWidth: 600 }}
      pagination={false}
      scroll={{ x: sum + 'px' }}
      className="tbody-center bg-white"
      rowClassName={(record, index) => {
        if (record.isError && index !== data.length - 1) return 'bg-danger'
      }}
      columns={columns}
      dataSource={data} />
    return (
      <div className="card card-body card-body-component">
        <button type="button" onClick={() => this.props.onClose(this.props.name)} className="btn btn-success" style={{ width: 100 }}>
          <FontAwesomeIcon icon="angle-double-left" />
          <span className="ml-2">Back</span>
        </button>
        {template.map((data, i) => {
          return <div key={i} className="form-row form-inline mb-2 col-12 px-0">
            <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">{data.label} :</label>
            <div className="col-lg-4 col-md-6 col-sm-8 col-12">
              {Render(data, this.onChange)}
            </div>
          </div>
        })}
        {table}
        <Divider />
        {/* footer */}
        <div className="text-center mt-2">
          <button
            onClick={() => this.props.onClose(this.props.name)}
            className="btn btn-light mr-2">
            Cancel
          </button>
          <button
            disabled={errors !== undefined || disabledSave}
            onClick={this.onSave}
            className="btn btn-success mr-2">
            Save
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
})

const mapDispatchToProps = dispatch => ({
  getListSettingTerminal: param => dispatch(getListSettingTerminal(param)),
  insertTerminal: body => dispatch(insertTerminal(body)),
  deleteTerminal: id => dispatch(deleteTerminal(id)),
  getTerminal: () => dispatch(getTerminal()),
  updateStatusTerminal: body => dispatch(updateStatusTerminal(body))
})

export default connect(mapStateToProps, mapDispatchToProps)(TerminalAdd)
