import React, { Component, Fragment } from 'react'
import Render from '../../components/Renderer'
import { UserConstraints as Constraints } from './Constraints'
import { checkEmailUser } from '../../redux/actions/user'
import { connect } from 'react-redux'

class UserDetail extends Component {
  state = {
    isLoading: false,
    isValidEmail: null,
    currentEmail: ''
  }

  checkEmail = value => {
    const { errors, form } = this.props
    if (errors['email'] || value.length === 0) return
    this.setState({ isLoading: true })
    const param = 'email=' + value + '&typeUser' + form.userType
    this.props.checkEmailUser(param).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        const data = res.payload.data.data
        if (data.isStatus === 1) {
          this.setState({ isLoading: false, isValidEmail: true, currentEmail: value })
        }
      }
    })
  }

  render() {
    const { form } = this.props
    const { isLoading, isValidEmail, currentEmail } = this.state
    const template = [{
      type: 'radiogroup',
      name: 'userType',
      label: 'User Type',
      value: form.userType,
      constraint: Constraints,
      placeholder: '',
      options: [
        { text: 'LDAP User', value: 2 },
        { text: 'System User', value: 1 }
      ]
    }, {
      type: 'search',
      enterButton: 'Check',
      name: 'email',
      loading: isLoading,
      label: 'Email',
      constraint: Constraints,
      value: form.email,
      onSearch: this.checkEmail,
      addon: isValidEmail !== null && {
        title: 'Notice',
        detail: currentEmail === form.email && isValidEmail ? 'This user is available' : 'This user is unavailable or already exist',
        icon: currentEmail === form.email && isValidEmail ? 'check' : 'info',
        color: currentEmail === form.email && isValidEmail ? '#52c41a' : 'orange'
      }
    }, {
      type: 'select',
      name: 'prefix',
      constraint: Constraints,
      label: 'Title',
      value: form.prefix,
      placeholder: 'Select Title...',
      options: {
        name: 'prefixOptions',
        id: 'prefixId',
        text: 'prefixName'
      }
    }, {
      type: 'input',
      name: 'firstName',
      constraint: Constraints,
      label: 'First Name',
      value: form.firstName,
    }, {
      type: 'input',
      name: 'lastName',
      constraint: Constraints,
      label: 'Last Name',
      value: form.lastName,
    }, !form.isExist && {
      type: 'input',
      name: 'password',
      constraint: Constraints,
      password: true,
      label: 'Password',
      value: form.password,
    }, !form.isExist && {
      type: 'input',
      name: 'confirmPassword',
      password: true,
      label: 'Confirm Password',
      value: form.confirmPassword,
      constraint: form.confirmPassword !== form.password && {
        confirmPassword: {
          presence: {
            allowEmpty: false,
            message: 'require'
          }
        }
      }
    }, {
      type: 'input',
      name: 'phone',
      constraint: Constraints,
      label: 'Phone Number',
      value: form.phone,
    }, {
      type: 'radiogroup',
      name: 'isDOAStaff',
      label: 'Organization',
      value: form.isDOAStaff,
      constraint: Constraints,
      placeholder: '',
      options: [
        { text: 'DOA Staff', value: 1 },
        { text: 'Airport Staff', value: 0 }
      ]
    }, form.isDOAStaff === 0 && {
      type: 'select',
      name: 'airportId',
      label: 'Airport',
      value: form.airportId,
      placeholder: 'Select Airport',
      options: {
        name: 'airportDOAOptions',
        id: 'airportID',
        text: 'airportName'
      }
    }, {
      type: 'select',
      name: 'userGroup',
      constraint: Constraints,
      label: 'User Group',
      value: form.userGroup,
      placeholder: 'Select User Group...',
      options: {
        name: 'groupOptions',
        id: 'userGroupId',
        text: 'userGroupName'
      }
    }, {
      type: 'switch',
      name: 'status',
      label: 'Status',
      value: form.status
    }]
    return (
      <Fragment>
        {template.map((data, i) => {
          return data && <div key={i} className="form-row form-inline mb-2 col-12">
            <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">{data.label} :</label>
            <div className="col-lg-4 col-md-6 col-sm-8 col-12">
              {Render(data, this.props.onChange)}
            </div>
          </div>
        })}
      </Fragment>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  checkEmailUser: param => dispatch(checkEmailUser(param)),
})

export default connect(mapStateToProps, mapDispatchToProps)(UserDetail)
