import React, { Component } from 'react'
import './Main.css'
import { connect } from 'react-redux'
import Router from '../Router'
import RouterAdminAirline from '../RouterAdminAirline'
import {
  getAirport,
  getAirline,
  getAirType,
  getFlightStatus,
  getApproveStatus,
  getFlightNo,
  getCallsign,
  getArrDep,
  getAirportDOA,
  getTraffType,
  getParkingBay,
  getZone,
  getCountry,
  getRegion,
  getBelt,
  getTerminal,
  getGate,
  getReasonFlightStatus,
  getGroup,
  getPrefix,
  getAirportType,
  getCountryInZone
} from '../../redux/actions/dropdown'
import { message } from 'antd'
import Login from '../Login'

class Main extends Component {
  onLoginDone = () => {
    const { dropdown } = this.props
    if (dropdown.airPortOptions.length === 0) this.props.getAirport()
    if (dropdown.airlineOptions.length === 0) this.props.getAirline()
    if (dropdown.airTypeOptions.length === 0) this.props.getAirType()
    if (dropdown.flightStatusOptions.length === 0) this.props.getFlightStatus()
    if (dropdown.approveStatusOptions.length === 0) this.props.getApproveStatus()
    if (dropdown.flightNoOptions.length === 0) this.props.getFlightNo()
    if (dropdown.callsignOptions.length === 0) this.props.getCallsign()
    if (dropdown.arrDepOptions.length === 0) this.props.getArrDep()
    if (dropdown.airportDOAOptions.length === 0) this.props.getAirportDOA()
    if (dropdown.traffTypeOptions.length === 0) this.props.getTraffType()
    if (dropdown.parkingBayOptions.listBay.length === 0) this.props.getParkingBay()
    if (dropdown.zoneOptions.length === 0) this.props.getZone()
    if (dropdown.countryOptions.length === 0) this.props.getCountry()
    if (dropdown.regionOptions.length === 0) this.props.getRegion()
    if (dropdown.beltOptions.length === 0) this.props.getBelt()
    if (dropdown.terminalOptions.length === 0) this.props.getTerminal()
    if (dropdown.gateOptions.length === 0) this.props.getGate()
    if (dropdown.reasonFlightStatusOptions.length === 0) this.props.getReasonFlightStatus()
    if (dropdown.groupOptions.length === 0) this.props.getGroup()
    if (dropdown.prefixOptions.length === 0) this.props.getPrefix()
    if (dropdown.airportTypeOptions.length === 0) this.props.getAirportType()
    if (dropdown.countryInZoneOptions.length === 0) this.props.getCountryInZone()
  }

  onError = (error) => {
    if (this.timeout) clearTimeout(this.timeout)
    this.timeout = setTimeout(() => {
      message.error('Connection Error : ' + JSON.stringify(error))
      this.props.setError(null)
    }, 1000)
  }

  render() {
    const { token, permission } = this.props
    if (!token) return <Login onLoginDone={this.onLoginDone} />
    const { error } = this.props
    if (error.length > 0) this.onError(error)
    if (permission && permission.isAdminAirline === 1) return <RouterAdminAirline />
    return <Router />
  }
}

const mapStateToProps = state => ({
  dropdown: state.dropdown,
  language: state.language,
  token: state.token,
  error: state.error,
  permission: state.permission
})

const mapDispatchToProps = dispatch => ({
  setError: data => dispatch({ type: 'REQUEST_ERROR', data }),
  getAirport: () => dispatch(getAirport()),
  getAirline: () => dispatch(getAirline()),
  getAirType: () => dispatch(getAirType()),
  getFlightStatus: () => dispatch(getFlightStatus()),
  getApproveStatus: () => dispatch(getApproveStatus()),
  getFlightNo: () => dispatch(getFlightNo()),
  getCallsign: () => dispatch(getCallsign()),
  getArrDep: () => dispatch(getArrDep()),
  getAirportDOA: () => dispatch(getAirportDOA()),
  getTraffType: () => dispatch(getTraffType()),
  getParkingBay: () => dispatch(getParkingBay()),
  getZone: () => dispatch(getZone()),
  getCountry: () => dispatch(getCountry()),
  getRegion: () => dispatch(getRegion()),
  getBelt: () => dispatch(getBelt()),
  getTerminal: () => dispatch(getTerminal()),
  getGate: () => dispatch(getGate()),
  getReasonFlightStatus: () => dispatch(getReasonFlightStatus()),
  getGroup: () => dispatch(getGroup()),
  getPrefix: () => dispatch(getPrefix()),
  getAirportType: () => dispatch(getAirportType()),
  getCountryInZone: () => dispatch(getCountryInZone()),
})

export default connect(mapStateToProps, mapDispatchToProps)(Main)