import React, { Component } from 'react'
import Render from '../../../components/Renderer'
import { Divider, message } from 'antd'
import validatejs from 'validate.js'
import { Constraints } from './Constraints'
import { connect } from 'react-redux'
import { getGate } from '../../../redux/actions/dropdown'
import { insertGate, updateGate } from '../../../redux/actions/setting'

class GateEdit extends Component {
  state = {
    form: {},
    disabledSave: false
  }

  componentDidMount() {
    const { selected, params } = this.props
    let { form } = this.state
    form = { ...selected, ...form, ...params }
    this.setState({ form, errors: validatejs(form, Constraints) })
  }

  onChange = ({ name, value, error }) => {
    let { form, errors } = this.state
    form[name] = value
    if (errors === undefined) errors = {}
    if (error === undefined) delete errors[name]
    else errors[name] = error.join()
    if (Object.keys(errors).length === 0) errors = undefined
    this.setState({ form, errors })
  }

  renderInput = (name, text, index) => {
    var template = {
      type: 'input',
      name: name,
      value: text,
      placeholder: '',
    }
    return Render(template, ({ name, value }) => this.onChangeTable(index, name, value))
  }

  onSave = () => {
    this.setState({ disabledSave: true }, () => {
      const { form } = this.state
      const { selected } = this.props
      if (selected) { // update
        const body = {
          gateId: form.gateId,
          airportId: form.airportId,
          terminalId: form.terminalId,
          gateEN: form.gateEN,
          gateTH: form.gateTH,
          remark: form.remark,
          isEnable: (form.display ? 1 : 0)
        }
        this.props.updateGate(body).then(res => {
          setTimeout(() => { this.setState({ disabledSave: false }) }, 500)
          if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
            message.success('Update Gate success')
            this.props.onRefresh(this.props.name)
            this.props.getGate()
          }
        })
      } else {
        const body = {
          "airportId": form.airportId,
          "terminalId": form.terminalId,
          "listGate": [{
            "gateEN": form.gateEN,
            "gateTH": form.gateTH,
            "remark": form.remark,
            "isEnable": (form.display ? 1 : 0)
          }]
        }
        this.props.insertGate(body).then(res => {
          setTimeout(() => { this.setState({ disabledSave: false }) }, 500)
          if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
            message.success('Insert Belt success')
            this.props.onRefresh(this.props.name)
            this.props.getGate()
          }
        })
      }
    })
  }

  render() {
    const { params, selected } = this.props
    const { form, errors, disabledSave } = this.state
    const template = [{
      type: 'select',
      name: 'airportId',
      label: 'Airport',
      constraint: Constraints,
      value: form.airportId,
      disabled: params !== undefined || selected,
      placeholder: 'Select Airport...',
      options: {
        name: 'airportDOAOptions',
        id: 'airportID',
        text: 'airportName'
      }
    }, {
      type: 'select',
      name: 'terminalId',
      constraint: Constraints,
      disabled: selected !== null,
      label: 'Terminal',
      value: form.terminalId,
      placeholder: 'Select Terminal...',
      options: {
        name: 'terminalOptions',
        id: 'terminalId',
        text: 'terminalName',
        preFilter: x => x.airportId === form.airportId
      }
    }, {
      type: 'input',
      name: 'gateEN',
      label: 'Gate',
      constraint: Constraints,
      value: form.gateEN,
      addonAfter: 'EN',
      placeholder: '',
    }, {
      type: 'input',
      name: 'gateTH',
      constraint: Constraints,
      value: form.gateTH,
      addonAfter: 'TH',
      placeholder: '',
    }, {
      type: 'switch',
      name: 'display',
      label: 'Display (On/Off)',
      value: form.display
    }, {
      type: 'textarea',
      name: 'remark',
      label: 'Remark',
      placeholder: 'remark',
      constraint: Constraints,
      value: form.remark,
    }]
    return (
      <div className="card card-body card-body-component">
        {template.map((data, i) => {
          return <div key={i} className="form-row form-inline mb-2 col-12 px-0">
            <label className="col-lg-2 col-md-3 col-sm-4 col-5 justify-content-end">{data.label && data.label + ' : '}</label>
            <div className="col-lg-4 col-md-6 col-sm-8 col-12">
              {Render(data, this.onChange)}
            </div>
          </div>
        })}
        <Divider />
        {/* footer */}
        <div className="text-center mt-2">
          <button
            onClick={() => this.props.onClose(this.props.name)}
            className="btn btn-light mr-2">
            Cancel
          </button>
          <button
            disabled={errors !== undefined || disabledSave}
            onClick={this.onSave}
            className="btn btn-success mr-2">
            Save
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language,
})

const mapDispatchToProps = dispatch => ({
  insertGate: body => dispatch(insertGate(body)),
  updateGate: body => dispatch(updateGate(body)),
  getGate: () => dispatch(getGate()),
})

export default connect(mapStateToProps, mapDispatchToProps)(GateEdit)
