import React, { Component } from 'react'
import { Button, Divider } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class ViewUserPermission extends Component {
  render() {
    const airports = [
      { id: 1, name: 'BURIRAM 1' },
      { id: 2, name: 'BURIRAM 2' },
      { id: 3, name: 'BURIRAM 3' },
      { id: 4, name: 'BURIRAM 4' },
      { id: 5, name: 'BURIRAM 5' },
      { id: 6, name: 'BURIRAM 6' },
      { id: 7, name: 'BURIRAM 7' },
      { id: 8, name: 'BURIRAM 8' },
      { id: 9, name: 'BURIRAM 9' },
      { id: 10, name: 'BURIRAM 10' },
    ]
    const access = [1, 3, 5]
    const include = airports.filter(x => access.indexOf(x.id) > -1)
    const exclude = airports.filter(x => access.indexOf(x.id) === -1)
    return (
      <div>
        <div className="card card-body bg-info-light mb-4">
          <div className="col-lg-12 col-md-12 col-sm-12 col-12 form-row form-inline">
            <div className="col-lg-6 col-md-6 col-sm-6 col-12">
              <FontAwesomeIcon icon="user" />
              <span className="ml-2">staff@mail.com</span>
            </div>
            <div className="col-lg-6 col-md-6 col-sm-6 col-12">
              <FontAwesomeIcon icon="plane" />
              <span className="ml-2">NOK AIR</span>
            </div>
          </div>
        </div>

        <div className="form-inline align-middle mb-2">
          <FontAwesomeIcon icon="plane" />
          <h5 className="my-0 ml-2">Access Airports</h5>
        </div>
        <div className="form-row form-inline mb-2">
          {include.map((a, i) => {
            let color = 'bg-success'
            return <Button
              key={i}
              // onClick={() => this.onClick(a)}
              className={"m-1 " + color + ' ' + (color === 'bg-light' ? '' : 'text-white')}
              style={{ width: 150 }}>{a.name}</Button>
          })}
        </div>

        <div className="form-inline align-middle mt-3">
          <FontAwesomeIcon icon="ban" />
          <h5 className="my-0 ml-2">None</h5>
        </div>
        <div className="form-row form-inline mb-2">
          {exclude.map((a, i) => {
            let color = 'bg-light'
            return <Button
              key={i}
              // onClick={() => this.onClick(a)}
              className={"m-1 " + color + ' ' + (color === 'bg-light' ? '' : 'text-white')}
              style={{ width: 150 }}>{a.name}</Button>
          })}
        </div>
        <Divider />
        {/* footer */}
        <div className="text-center mt-2">
          <button
            onClick={this.props.onClose}
            className="btn btn-light mr-2">
            Close
          </button>
        </div>
      </div>
    );
  }
}

export default ViewUserPermission