import React, { Component } from 'react'
import moment from 'moment'
import TM from 'react-calendar-timeline/lib'
import './Timeline.css'

class Timeline extends Component {
  renderTopLeftTable = () => {
    return <div className="custom-top-left-table w-100 h-100">
      <span>TIME</span>
      <div>DATE</div>
    </div>
  }

  render() {
    const { groups, items, itemRenderer, startDate, endDate } = this.props
    return (
      <div className="custom-timeline">
        <TM
          sidebarContent={this.renderTopLeftTable()}
          groups={groups}
          items={items}
          canMove={false}
          canChangeGroup={false}
          canResize={false}
          stackItems
          headerLabelGroupHeight={40}
          headerLabelHeight={40}
          lineHeight={40}
          itemHeightRatio={0.75}
          itemRenderer={itemRenderer}
          defaultTimeStart={moment(startDate)}
          defaultTimeEnd={moment(endDate)}
        />
      </div>
    );
  }
}

export default Timeline