import React, { Component } from 'react'
import Render from '../../../components/Renderer'
import { Divider, message } from 'antd'
import { connect } from 'react-redux'
import { Constraints } from './Constraints'
import validatejs from 'validate.js'
import { getTraffType } from '../../../redux/actions/dropdown'
import { updateTraffType, insertTraffType } from '../../../redux/actions/setting'

class TraffTypeEdit extends Component {
  state = {
    form: {},
    disabledSave: false
  }

  componentDidMount() {
    const { selected, data } = this.props
    let { form } = this.state
    form = { ...selected, ...form }
    this.setState({ form, errors: validatejs(form, Constraints), data: data || [{ key: 1 }] })
  }

  onChange = ({ name, value, error }) => {
    let { form, errors } = this.state
    form[name] = value
    if (errors === undefined) errors = {}
    if (error === undefined) delete errors[name]
    else errors[name] = error.join()
    if (Object.keys(errors).length === 0) errors = undefined
    this.setState({ form, errors })
  }

  onSave = () => {
    this.setState({ disabledSave: true }, () => {
      const { selected } = this.props
      const { form } = this.state
      let body = {
        traffTypeCode: form.traffType,
        traffTypeDescTh: form.descriptionTh,
        traffTypeDescEn: form.descriptionEn,
        traffTypeGroup: form.traffTypeGroup,
        trainingStatus: form.trainingStatus ? 1 : 0,
        isEnable: form.display ? 1 : 0,
        scheduleType: form.scheduleType,
        commercialType: form.commercialType
      }
      if (!selected) {
        this.props.insertTraffType(body).then(res => {
          setTimeout(() => { this.setState({ disabledSave: false }) }, 500)
          if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
            body.traffTypeId = res.payload.data.data.id
            this.props.getTraffType().then(res => {
              message.success('Insert Traff Type ' + body.traffTypeCode + ' success')
              this.props.onRefresh(this.props.name, body)
            })
          }
        })
      } else {
        body.traffTypeId = selected.traffTypeId
        this.props.updateTraffType(body).then(res => {
          setTimeout(() => { this.setState({ disabledSave: false }) }, 500)
          if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
            this.props.getTraffType().then(res => {
              message.success('Update Traff Type ' + body.traffTypeCode + ' success')
              this.props.onRefresh(this.props.name, body)
            })
          }
        })
      }
    })
  }

  render() {
    const { form, errors, disabledSave } = this.state
    const template = [{
      type: 'input',
      name: 'traffType',
      label: 'Traff Type',
      value: form.traffType,
      constraint: Constraints,
      placeholder: '',
    }, {
      type: 'input',
      name: 'descriptionEn',
      label: 'Description',
      value: form.descriptionEn,
      constraint: Constraints,
      placeholder: '',
      addonAfter: 'EN'
    }, {
      type: 'input',
      name: 'descriptionTh',
      label: '',
      value: form.descriptionTh,
      constraint: Constraints,
      placeholder: '',
      addonAfter: 'TH'
    }, {
      type: 'radiogroup',
      name: 'traffTypeGroup',
      label: 'Inter / Dom',
      value: form.traffTypeGroup,
      constraint: Constraints,
      placeholder: '',
      options: [
        { text: 'Domestic Flight', value: 1 },
        { text: 'International Flight', value: 2 }
      ]
    }, {
      type: 'radiogroup',
      name: 'scheduleType',
      label: 'Sch / Non-Sch',
      value: form.scheduleType,
      constraint: Constraints,
      placeholder: '',
      options: [
        { text: 'Scheduled Flight', value: 1 },
        { text: 'Non-Scheduled Flight', value: 2 }
      ]
    }, {
      type: 'radiogroup',
      name: 'commercialType',
      label: 'Com / Non-Com',
      value: form.commercialType,
      constraint: Constraints,
      placeholder: '',
      options: [
        { text: 'Commercial Flight', value: 1 },
        { text: 'Non-Commercial Flight', value: 2 }
      ]
    }, form.commercialType === 2 && {
      type: 'checkbox',
      name: 'trainingStatus',
      label: 'Training Flight',
      value: form.trainingStatus,
      placeholder: '',
    }, {
      type: 'switch',
      name: 'display',
      label: 'Display (On/Off)',
      value: form.display
    }
    ]
    return (
      <div className="card card-body card-body-component">
        {template.map((data, i) => {
          return <div key={i} className="form-row form-inline mb-2 col-12">
            <label className="col-lg-3 col-md-4 col-sm-4 col-12 justify-content-end">{data.label ? data.label + ' : ' : ''}</label>
            <div className="col-lg-8 col-md-7 col-sm-6 col-12">
              {Render(data, this.onChange)}
            </div>
          </div>
        })}

        <Divider />

        {/* footer */}
        <div className="text-center mt-2">
          <button
            onClick={() => this.props.onClose(this.props.name)}
            className="btn btn-light mr-2">
            Cancel
          </button>
          <button
            disabled={errors !== undefined || disabledSave}
            onClick={this.onSave}
            className="btn btn-success mr-2">
            Save
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  updateTraffType: body => dispatch(updateTraffType(body)),
  insertTraffType: body => dispatch(insertTraffType(body)),
  getTraffType: () => dispatch(getTraffType())
})

export default connect(mapStateToProps, mapDispatchToProps)(TraffTypeEdit)
