import React, { Component } from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Table, Switch, Modal, Spin } from 'antd'
import ViewUserPermission from './ViewUserPermission'
import { isReadOnly } from '../../utils/permission'

class ViewUserAccountTable extends Component {
  state = {
    isOpenModal: false,
    id: null
  }

  view = id => {
    this.setState({ isOpenModal: true, id })
  }

  onCloseModal = () => this.setState({ isOpenModal: false })

  render() {
    const { isLoading, data } = this.props
    const { isOpenModal, id } = this.state
    const readOnly = isReadOnly('airline')
    const dataTable = data && data.response && data.response.filter(x => x.isAdmin !== 1).map((d, i) => ({
      key: i,
      no: i + 1,
      fullName: d.fullName,
      userId: d.userId,
      email: d.email,
      lastLogin: d.lastLogin,
      status: d.isEnable === 1
    }))
    const columns = [
      {
        title: '#',
        dataIndex: 'no',
        key: 'no',
        width: 50
      }, {
        title: 'Name',
        dataIndex: 'fullName',
        key: 'fullName',
        align: 'left',
        width: 200
      }, {
        title: 'Email',
        dataIndex: 'email',
        key: 'email',
        align: 'left',
        width: 200
      }, {
        title: 'View Airport Permission',
        key: 'view',
        width: 120,
        render: item => (
          <button onClick={() => this.view(item.id)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="file" />
          </button>
        )
      }, {
        title: 'Status',
        key: 'status',
        width: 80,
        render: item => <Switch
          disabled={readOnly}
          onChange={checked => this.props.onCheckChange(item, checked)}
          checked={item.status} />
      }, {
        title: 'Last login',
        dataIndex: 'lastLogin',
        key: 'lastLogin',
        width: 80
      }
    ]
    const sum = columns.reduce((a, b) => a + b.width, 0)
    const table = <Table
      className="tbody-center bg-white"
      columns={columns}
      footer={isLoading ? () => <div className="text-center"><Spin className="pr-2" />loading...</div> : null}
      pagination={{
        defaultPageSize: 10,
        position: 'bottom'
      }}
      scroll={{ x: sum + 'px' }}
      dataSource={dataTable} />

    return (
      <div className="card card-body card-header-table">
        {table}

        <Modal
          title={'Airport Permission'}
          width="900px"
          onCancel={this.onCloseModal}
          destroyOnClose={true}
          footer={null}
          visible={isOpenModal}>
          <ViewUserPermission id={id} onClose={this.onCloseModal} />
        </Modal>
      </div>
    )
  }
}

export default ViewUserAccountTable
