import React, { Component } from 'react'
import { Table, Switch, Popover, message, Spin } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'
import { isReadOnly } from '../../utils/permission'
import Render from '../../components/Renderer'
import { getListFormManagement, deleteFormManagement } from '../../redux/actions/formmanagement'

class FormManagement extends Component {
  state = {
    form: {},
    isLoading: false,
    data: [],
    visible: {},
  }

  componentDidMount() {
    this.getList()
  }

  getList = param => {
    this.setState({ isLoading: true })
    this.props.getListFormManagement(param).then(res => {
      this.setState({ isLoading: false })
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        let data = res.payload.data.data
        data.forEach((x, i) => {
          x.key = i
          x.no = i + 1
          x.display = x.isEnable === 1
        })
        this.setState({ data })
      }
    })
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form, data: [] }, this.search)
  }

  search = () => {
    let { form } = this.state
    let param = ''
    if (form.formName) param = 'formName=' + form.airline
    this.getList(param)
  }

  add = () => {
    const { history } = this.props
    history && history.push('/formmanagement/add')
  }

  download = (item) => {

  }

  view = (item) => {

  }

  edit = (item) => {
    const { history } = this.props
    history && history.push('/formmanagement/edit/' + item.formId)
  }

  delete = item => {
    let { visible } = this.state
    visible[item.key] = false
    this.setState({ visible, data: [] })
    this.props.deleteFormManagement(item.formId).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        message.success('Delete Form success')
        this.search()
      }
    })
  }

  render() {
    const { form, data, isLoading } = this.state
    const readOnly = isReadOnly('formmanagement')
    const template = [{
      type: 'search',
      name: 'formName',
      value: form.formName,
      placeholder: 'Form Name',
    }]
    const columns = [
      {
        title: '#',
        key: 'no',
        dataIndex: 'no',
        width: 80
      }, {
        title: 'Form Name',
        align: 'left',
        dataIndex: 'formName',
        key: 'formName',
        width: 200
      }, {
        title: 'Display (On/Off)',
        key: 'display',
        width: 150,
        render: item => <Switch
          disabled={readOnly}
          onChange={checked => this.onCheckChange(item, checked)}
          checked={item.display} />
      }, {
        title: 'Update By',
        dataIndex: 'nameUpdate',
        width: 250,
        key: 'nameUpdate'
      }, {
        title: 'Update Date',
        dataIndex: 'dateUpdate',
        width: 250,
        key: 'dateUpdate'
      }, {
        title: 'Download Form',
        key: 'download',
        width: 120,
        render: item => <button disabled={readOnly} onClick={() => this.download(item)} type="button" className="btn btn-outline-primary btn-sm">
          <FontAwesomeIcon icon="download" />
        </button>
      }, {
        title: 'View File',
        key: 'view',
        width: 80,
        render: item => <button disabled={readOnly} onClick={() => this.view(item)} type="button" className="btn btn-outline-primary btn-sm">
          <FontAwesomeIcon icon="file" />
        </button>
      }, {
        title: 'Edit Structure',
        key: 'edit',
        width: 120,
        render: item => <button disabled={readOnly} onClick={() => this.edit(item)} type="button" className="btn btn-outline-warning btn-sm">
          <FontAwesomeIcon icon="pen" />
        </button>
      }, {
        title: 'Delete',
        key: 'delete',
        width: 100,
        render: item => (
          readOnly ? <button disabled type="button" className="btn btn-outline-danger btn-sm">
            <FontAwesomeIcon icon="trash" />
          </button> :
            <Popover
              visible={this.state.visible[item.key]}
              onVisibleChange={_visible => {
                let { visible } = this.state
                visible[item.key] = _visible
                this.setState({ visible })
              }}
              content={<button
                type="button"
                className="btn btn-outline-danger btn-sm"
                onClick={() => this.delete(item)}>Delete</button>}
              title="Are you sure?"
              trigger="click">
              <button disabled={readOnly} type="button" className="btn btn-outline-danger btn-sm">
                <FontAwesomeIcon icon="trash" />
              </button>
            </Popover>
        )
      }
    ]
    const sum = columns.reduce((a, b) => a + b.width, 0)
    const table = <Table
      pagination={{
        defaultPageSize: 10,
        position: 'bottom'
      }}
      footer={isLoading ? () => <div className="text-center"><Spin className="pr-2" />loading...</div> : null}
      scroll={{ x: sum + 'px' }}
      className="tbody-center bg-white"
      columns={columns}
      dataSource={data} />
    return (
      <div className="card card-body card-body-component">
        <div className="form-row form-inline col-md-12 mb-3 px-0">
          <div className="col-md-3">
            {!readOnly && <button type="button" onClick={this.add} className="btn btn-success">
              <FontAwesomeIcon icon="plus" /> Add Form
            </button>}
          </div>
          <div className="col-md-9 form-inline justify-content-end px-0">
            {template.map((t, i) => (<div className="col-md-2 justify-content-end pr-0" key={i}>
              {Render(t, this.onChange)}
            </div>))}
            <button onClick={() => this.setState({ form: {} }, this.search)} className="btn btn-sm btn-dark ml-2">
              <FontAwesomeIcon icon="redo" />
              <span className="ml-2">clear</span>
            </button>
          </div>
        </div>
        {table}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  getListFormManagement: param => dispatch(getListFormManagement(param)),
  deleteFormManagement: id => dispatch(deleteFormManagement(id))
})

export default connect(mapStateToProps, mapDispatchToProps)(FormManagement)
