import React, { Component } from 'react'
import { Table } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'
import { isReadOnly } from '../../utils/permission'

class ResultTable extends Component {
  view = item => {
    const { history } = this.props
    history.push('/flightschedule/detail/' + item.flightPlanId)
  }

  edit = item => {
    const { history } = this.props
    const id = item ? item.flightPlanId : null
    if (id) history.push('/flightschedule/edit/' + id)
    else history.push('/flightschedule/add')
  }

  render() {
    const { data } = this.props
    const readOnly = isReadOnly('flightschedule')
    let dataTable = data.map((d, i) => ({
      key: i,
      flightPlanId: d.flightPlanScheduleId,
      from: d.airportNameArr,
      to: d.airportNameDep,
      flightDate: d.dateStartFlightPlanTxt && d.dateEndFlightPlanTxt && d.dateStartFlightPlanTxt + ' - ' + d.dateEndFlightPlanTxt,
      traffType: d.traffType,
      lastUpdate: d.dateUpdate,
      updateBy: d.updateBy
    }))
    dataTable.sort((a, b) => b.lastUpdate.localeCompare(a.lastUpdate))

    const columns = [
      {
        title: 'Route',
        dataIndex: 'from',
        key: 'from',
        colSpan: 3,
        width: 250
      },
      {
        colSpan: 0,
        width: 80,
        render: () => (
          <span>
            <FontAwesomeIcon icon="exchange-alt" className="text-primary" />
          </span>
        )
      },
      {
        title: 'route',
        dataIndex: 'to',
        key: 'to',
        colSpan: 0,
        width: 250
      },
      {
        title: 'Date',
        dataIndex: 'flightDate',
        key: 'flightDate'
      },
      {
        title: 'Traff Type',
        dataIndex: 'traffType',
        key: 'traffType'
      },
      {
        title: 'Update Date',
        dataIndex: 'lastUpdate',
        key: 'lastUpdate'
      },
      {
        title: 'View',
        key: 'view',
        render: item => (
          <button onClick={() => this.view(item)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="file" />
          </button>
        )
      },
      {
        title: 'Edit',
        key: 'edit',
        render: item => (
          <button disabled={readOnly} onClick={() => this.edit(item)} type="button" className="btn btn-outline-warning btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }
    ]

    const table = <Table
      scroll={{ x: '1500px' }}
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      dataSource={dataTable} />

    return (
      <div className="card card-body card-header-table">
        {!readOnly && <div className="mb-3">
          <button type="button" onClick={() => this.edit()} className="btn btn-success">
            <FontAwesomeIcon icon="plus" /> Add Flight Schedule
          </button>
        </div>}
        {table}
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

export default connect(mapStateToProps)(ResultTable)
