import React, { Component } from 'react'
import { Modal, Button, Select, Radio, message } from 'antd'
import { connect } from 'react-redux'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import Render from '../../components/Renderer'
import context from '../../utils/language'
import { updateStatusFlightSchedule } from '../../redux/actions/flight'
import { Constraints } from './Constraints'
import validatejs from 'validate.js'

class ModalStatus extends Component {
  state = {
    form: {},
    errors: undefined,
    selected: {},
    isLoading: false
  }

  merge = props => {
    const { selectedFlight } = props
    this.setState({ form: selectedFlight, errors: validatejs(selectedFlight, Constraints) })
  }

  componentDidMount() {
    this.merge(this.props)
  }

  componentWillReceiveProps(nextProps) {
    this.merge(nextProps)
  }

  onChange = ({ name, value, error, selected }) => {
    let { form, errors } = this.state
    form[name] = value
    if (name === 'flightStatusId') form.flightStatusName = selected.flightStatusName
    if (name === 'cause') form.causeText = selected.reasonFlightStatus
    if (errors === undefined) errors = {}
    if (error === undefined) delete errors[name]
    else errors[name] = error.join()
    if (Object.keys(errors).length === 0) errors = undefined
    this.setState({ form, errors, selected })
  }

  onSave = () => {
    this.setState({ isLoading: true })
    const { form } = this.state
    let body = {
      flightScheduleId: form.flightScheduleId,
      codeShare: form.codeShare,
      flightStatusID: form.flightStatusId,
      flightStatusParentID: form.cause,
      timeDepArr: (form.type === 'arrival' ? form.arrivalTime : form.departureTime),
      callSignId: form.callsignId,
      airtypeId: form.airtypeId,
      terminalId: form.terminalId,
      beltId: form.belt,
      gateId: form.gate,
      reasonDelay: form.causeText,
      remark: form.remark,
      isHoleOnRunWay: form.parkingType,
      parkingId: form.parkingId
    }
    this.props.updateStatusFlightSchedule(body).then(res => {
      this.setState({ isLoading: false })
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        message.success('Update status success')
        this.props.onRefresh()
      }
    })
  }

  render() {
    const { open, dropdown, language } = this.props
    let { form, errors, isLoading } = this.state
    const name = context[language].viewStatModal
    let template = [
      {
        type: 'input',
        isFull: true,
        name: 'codeShare',
        label: 'Code share',
        value: form.codeShare,
        placeholder: name && name.codeSharePlaceholder,
      }, {
        type: 'select',
        name: 'flightStatusId',
        label: name && name.flightStatus,
        value: form.flightStatusId,
        placeholder: name && name.flightStatusPlaceholder,
        options: {
          name: 'flightStatusOptions',
          id: 'configFlightStatusId',
          text: 'flightStatusName',
          preFilter: x => x.arrivalDepartureStatus === (form.type === "arrival" ? 'A' : 'D')
        }
      }, {
        type: 'timepicker',
        name: form.type === 'arrival' ? 'arrivalTime' : 'departureTime',
        label: name && name.time,
        value: form.type === 'arrival' ? form.arrivalTime : form.departureTime,
        placeholder: name && name.timePlaceholder,
      }, form.flightStatusName === 'Delayed' && {
        type: 'select',
        name: 'cause',
        isFull: true,
        label: name && name.cause,
        value: form.cause,
        placeholder: name && name.causePlaceholder,
        options: {
          name: 'reasonFlightStatusOptions',
          id: 'parentFlightStatusId',
          text: 'reasonFlightStatus',
          preFilter: x => x.arrDepStatus === (form.type === "arrival" ? 'A' : 'D') &&
            x.configFlightStatusId === form.flightStatusId
        }
      }, {
        type: 'select',
        name: 'callsignId',
        label: name && name.callsign,
        value: form.callsignId,
        placeholder: name && name.callsignPlaceholder,
        options: {
          name: 'callsignOptions',
          id: 'callsignID',
          text: 'callsignCode'
        }
      }, {
        type: 'select',
        name: 'airtypeId',
        label: name && name.airtype,
        value: form.airtypeId,
        placeholder: name && name.airtypePlaceholder,
        options: {
          name: 'airTypeOptions',
          id: 'airTypeID',
          text: 'airType',
          filter: (thing, index, self) => index === self.findIndex((t) => (t.id === thing.id))
        }
      }, {
        type: 'select',
        name: 'terminalId',
        label: 'Terminal',
        value: form.terminalId,
        placeholder: name && name.terminalPlaceholder,
        options: {
          name: 'terminalOptions',
          id: 'terminalId',
          text: 'terminalName'
        }
      }, form.type === 'arrival' && {
        type: 'select',
        name: 'belt',
        label: 'Belt',
        value: form.belt,
        disabled: !form.terminalId,
        placeholder: name && name.beltPlaceholder,
        options: {
          name: 'beltOptions',
          id: 'beltNo',
          text: 'beltName',
          preFilter: x => x.terminalId === form.terminalId
        }
      }, form.type === 'departure' && {
        type: 'select',
        name: 'gate',
        label: 'Gate',
        value: form.gete,
        disabled: !form.terminalId,
        placeholder: name && name.getePlaceholder,
        options: {
          name: 'gateOptions',
          id: 'gateId',
          text: 'gateName',
          preFilter: x => x.terminalId === form.terminalId
        }
      }
    ]
    template.forEach(x => x !== false && (x.constraint = Constraints))

    const remark = {
      type: 'input',
      name: 'remark',
      label: 'Flight Remark',
      value: form.remark,
      constraint: Constraints,
      placeholder: name && name.beltPlaceholder,
    }
    if (form.parkingId === 0) delete form.parkingId
    const pitOption = form.parkingType === 1 ?
      dropdown.parkingBayOptions.listBay.map(x => ({ value: x.bayId, text: x.text })) :
      dropdown.parkingBayOptions.listSlot.map(x => ({ value: x.slotId, text: x.text }))
    return (
      <Modal
        title={'Update Flight Status'}
        visible={open}
        width="850px"
        onCancel={this.props.onClose}
        destroyOnClose={true}
        footer={null}>
        <Button type="primary" shape="circle">
          <FontAwesomeIcon icon={form.type === 'arrival' ? "plane-arrival" : 'plane-departure'} />
        </Button>
        <label className="ml-2"><h5>{form.type === 'arrival' ? 'Arrival' : 'Departure'}</h5></label>

        <div className="card card-body bg-primary-light mb-4 col-sm-12">
          <div className="form-row">
            <div className="col col-sm-4">
              <div>Flight No</div>
              <h5 className="mb-0">{form.flightNo}</h5>
              <div>
                <img src={form.airlineImg || ''} width="50" height="25" alt="" />
                <span className="ml-2 font-weight-bold">{form.airportName}</span>
              </div>
            </div>
            <div className="col col-sm-4">
              <div>From</div>
              <h5 className="mb-0">{form.arrivalAirportName} <FontAwesomeIcon className="fa-lg pt-1 pl-2" icon="plane" /></h5>
              <div>{form.arrivalTime}</div>
            </div>
            <div className="col col-sm-4">
              <div>To</div>
              <h5 className="mb-0">{form.departureAirportName}</h5>
              <div>{form.departureTime}</div>
            </div>
          </div>
        </div>

        <div className="form-row">
          {template.map((t, i) => (
            t && <div key={i} className={t.isFull ? "col-sm-12 form-inline mb-2" : "col-sm-6 form-inline mb-2"}>
              <label className={"justify-content-end pr-0 " + (t.isFull ? 'col-sm-2' : 'col-sm-4')}>{t.label} :</label>
              <div className={t.isFull ? "col-sm-10" : "col-sm-8"}>
                {Render(t, this.onChange)}
              </div>
            </div>
          ))}
        </div>

        <div className="form-row">
          <div className="col-sm-12 form-inline mb-2">
            <label className="col-sm-2 justify-content-end pr-0">{remark.label} :</label>
            <div className="col-sm-10">
              {Render(remark, this.onChange)}
            </div>
          </div>
        </div>

        {/* Pit Parking */}
        <div className="col-sm-12 form-inline mb-2">
          <label className="col-sm-2 justify-content-end pr-0">{name && name.pitParking} :</label>
          <div className="col-sm-10">
            <Radio.Group
              name={'parkingType'}
              onChange={e => this.onChange({ name: 'parkingType', value: e.target.value })}
              value={form.parkingType}>
              <Radio value={0}>{name && name.unknown}</Radio>
              <Radio value={1}>{name && name.pit}</Radio>
              <Radio value={2}>{name && name.parking}</Radio>
            </Radio.Group>
          </div>
        </div>
        <div className="col-sm-12 form-inline mb-2">
          <label className="col-sm-2 justify-content-end"></label>
          {(form.parkingType === 1 || form.parkingType === 2) && <div className="col-sm-10 pl-0 pr-0">
            <Select
              name={'parkingId'}
              showSearch
              style={{ width: '100%' }}
              placeholder={name && name.selectPitParkingPlaceholder}
              optionFilterProp="children"
              onChange={value => this.onChange({ name: 'parkingId', value: value })}
              filterOption={(input, option) =>
                option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
              }
              value={form.parkingId}>
              {pitOption.map((op, i) => (<Select.Option key={i} value={op.value}>{op.text}</Select.Option>))}
            </Select>
          </div>}
        </div>

        {/* footer */}
        <div className="text-center mt-2">
          <Button
            onClick={this.props.onClose}
            className="btn-light mr-2">
            {name && name.cancel}
          </Button>
          <Button
            loading={isLoading}
            disabled={errors !== undefined}
            onClick={this.onSave}
            className="btn-success">
            <span>{name && name.save}</span>
          </Button >
        </div>

      </Modal>
    )
  }
}

const mapStateToProps = state => ({
  language: state.language,
  dropdown: state.dropdown
})

const mapDispatchToProps = dispatch => ({
  updateStatusFlightSchedule: body => dispatch(updateStatusFlightSchedule(body)),
})

export default connect(mapStateToProps, mapDispatchToProps)(ModalStatus)
