import React, { Component } from 'react'
import { Table, Divider, Popover, message, Spin } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'
import { isReadOnly } from '../../../utils/permission'
import Render from '../../../components/Renderer'
import { getListSettingManageRelate, deleteManageRelation } from '../../../redux/actions/setting'
//import context from '../../../utils/language'

class Relation extends Component {
  state = {
    form: {},
    data: [],
    isLoading: false,
    visible: {}
  }

  componentDidMount() {
    this.getList()
  }

  componentWillReceiveProps(nextProps) {
    const { params } = nextProps
    if (params) this.setState({ form: { airline: params.airlineId } }, this.search)
  }

  getList = param => {
    this.setState({ data: [], isLoading: true })
    this.props.getListSettingManageRelate(param).then(res => {
      this.setState({ isLoading: false })
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        const data = res.payload.data.data
        let array = [], index = 1
        for (let airline of data) {
          for (let callsign of airline.listCallsign) {
            array.push({
              key: index,
              no: index,
              ownerType: callsign.ownerType,
              ownerId: callsign.ownerId,
              ownerOther: callsign.ownerOther,
              airlineId: airline.airlineId,
              callsign: callsign.callsignCode,
              callsignId: callsign.callsignId,
              airline: airline.airlineName,
              companyName: callsign.airlineCompany,
              display: true,
              updateDate: callsign.dateUpdate,
              list: callsign.listAirtype
            })
            ++index
          }
        }
        this.setState({ data: array })
      }
    })
  }

  search = () => {
    let { form } = this.state
    let param = ''
    param += 'airlineId=' + (form.airline || '')
    param += '&callsignId=' + (form.callsign || '')
    param += '&airtypeId=' + (form.airTypeID || '')
    param += '&grossPax=' + (form.grossweight || '')
    this.getList(param)
  }

  edit = (item) => this.props.onEdit(this.props.name, item)
  add = () => this.props.onAdd(this.props.name)

  onDelete = item => {
    let { visible } = this.state
    visible[item.key] = false
    this.setState({ visible, data: [] })
    const body = {
      airlineId: item.airlineId,
      callsignId: item.callsignId
    }
    this.props.deleteManageRelation(body).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        message.success('Delete Relation success')
        this.search()
      }
    })
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form, data: [] }, this.search)
  }

  refresh = () => {
    this.props.onRefreshDone()
    setTimeout(() => { this.search() }, 200)
  }

  render() {
    const { form, data, isLoading } = this.state
    const { refresh } = this.props
    if (refresh) this.refresh()
    //const { language } = this.props
    //const name = context[language].viewStatModal
    const readOnly = isReadOnly('airtypesetting')
    const template = [{
      type: 'select',
      name: 'airline',
      value: form.airline,
      placeholder: 'Select Airline...',
      options: {
        name: 'airlineOptions',
        id: 'airlineID',
        text: 'showFilter'
      }
    }, {
      type: 'select',
      name: 'callsign',
      value: form.callsign,
      placeholder: 'Select Callsign...',
      options: {
        name: 'callsignOptions',
        id: 'callsignID',
        text: 'callsignCode'
      }
    }, {
      type: 'select',
      name: 'airTypeID',
      value: form.airTypeID,
      placeholder: 'Select Air Type...',
      options: {
        name: 'airTypeOptions',
        id: 'airTypeID',
        text: 'airType',
        filter: (thing, index, self) => index === self.findIndex((t) => (t.id === thing.id))
      }
    }, {
      type: 'search',
      name: 'grossweight',
      value: form.grossweight,
      placeholder: 'Gross Weight, Pax...',
    }]
    const columns = [
      {
        title: '#',
        key: 'no',
        dataIndex: 'no',
        align: 'center',
        width: 50,
      }, {
        title: 'Callsign',
        dataIndex: 'callsign',
        key: 'callsign',
        align: 'left',
        width: 150,
      }, {
        title: 'Airline',
        dataIndex: 'airline',
        key: 'airline',
        align: 'left',
        width: 150,
      }, {
        title: 'Owner',
        dataIndex: 'companyName',
        key: 'companyName',
        align: 'left',
        width: 150,
      }, {
        title: 'Update Date',
        dataIndex: 'updateDate',
        key: 'updateDate',
        align: 'center',
        width: 150,
      }, {
        title: 'Edit',
        key: 'edit',
        align: 'center',
        width: 80,
        render: item => (
          <button disabled={readOnly} onClick={() => this.edit(item)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: 'Delete',
        key: 'delete',
        align: 'center',
        width: 80,
        render: item => (
          readOnly ? <button disabled type="button" className="btn btn-outline-danger btn-sm">
            <FontAwesomeIcon icon="trash" />
          </button> :
            <Popover
              visible={this.state.visible[item.key]}
              onVisibleChange={_visible => {
                let { visible } = this.state
                visible[item.key] = _visible
                this.setState({ visible })
              }}
              content={<button
                type="button"
                className="btn btn-outline-danger btn-sm"
                onClick={() => this.onDelete(item)}>Delete</button>}
              title="Are you sure?"
              trigger="click">
              <button disabled={readOnly} onClick={this.delete} type="button" className="btn btn-outline-danger btn-sm">
                <FontAwesomeIcon icon="trash" />
              </button>
            </Popover>
        )
      }
    ]

    const table = <Table
      scroll={{ x: '1500px', y: 400 }}
      pagination={{
        defaultPageSize: 10,
        position: 'bottom'
      }}
      footer={isLoading ? () => <div className="text-center"><Spin className="pr-2" />loading...</div> : null}
      className="bg-white"
      columns={columns}
      expandedRowRender={record => {
        return record.list.map((l, i) => (
          <div className="form-inline mb-2" key={i}>
            <div className="col-md-1 text-right">{i + 1}<Divider type="vertical" /></div>
            <div className="col-md-2 text-left">Air Type :{l.airtypeId}</div>
            <div className="col-md-3 text-left">Gross Weight :{l.grossWeight}</div>
            <div className="col-md-2 text-left">Pax :{l.pax}</div>
          </div>
        ))
      }}
      dataSource={data} />
    return (
      <div className="card card-body card-body-component p-0">
        <div className="card card-body card-header-table">
          <div className="form-row form-inline col-md-12 mb-3 px-0">
            <div className="col-md-3">
              {!readOnly && <button type="button" onClick={this.add} className="btn btn-success">
                <FontAwesomeIcon icon="plus" /> Add Relation
            </button>}
            </div>
            <div className="col-md-9 form-inline justify-content-end px-0">
              {template.map((t, i) => (<div className="col-md-2 justify-content-end pr-0" key={i}>
                {Render(t, this.onChange)}
              </div>))}
              <button onClick={() => this.setState({ form: {} }, this.search)} className="btn btn-sm btn-dark ml-2">
                <FontAwesomeIcon icon="redo" />
                <span className="ml-2">clear</span>
              </button>
            </div>
          </div>
          {table}
        </div>
      </div>
    );
  }
}


const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  getListSettingManageRelate: param => dispatch(getListSettingManageRelate(param)),
  deleteManageRelation: body => dispatch(deleteManageRelation(body)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Relation)
