import React, { Component } from 'react'
import { Table, Switch, Divider, Popover, message, Spin } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { connect } from 'react-redux'
import { isReadOnly } from '../../../utils/permission'
import Render from '../../../components/Renderer'
import { getListSettingAirtype, deleteAirType, updateDisplayStatusAirType } from '../../../redux/actions/setting'
import { getAirType } from '../../../redux/actions/dropdown'
//import context from '../../../utils/language'

class AirType extends Component {
  state = {
    form: {},
    data: [],
    visible: {},
    isLoading: false,
  }

  componentDidMount() {
    this.getList()
  }

  getList = param => {
    this.setState({ isLoading: true })
    this.props.getListSettingAirtype(param).then(res => {
      this.setState({ isLoading: false })
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        const data = res.payload.data.data
        let array = [], distinct = {}
        for (let [index, item] of data.entries()) {
          if (distinct[item.airTypeCode] === undefined) array.push({
            key: index,
            no: index + 1,
            airTypeId: item.airTypeId,
            airtype: item.airTypeCode,
            display: item.isEnable === 1,
            updateDate: item.dateUpdate,
            list: data.filter(x => x.airTypeCode === item.airTypeCode).map(x => ({
              airTypeId: item.airTypeId,
              grossWeight: x.grossWeight,
              pax: x.pax
            }))
          })
          distinct[item.airTypeCode] = true
        }
        array.sort((a, b) => a.airtype - b.airtype)
        this.setState({ data: array })
      }
    })
  }

  edit = (item) => this.props.onEdit(this.props.name, item)
  add = () => this.props.onAdd(this.props.name)

  onDelete = item => {
    let { visible } = this.state
    visible[item.key] = false
    this.setState({ visible, data: [] })
    this.props.deleteAirType(item.airTypeId).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        message.success('Delete Air Type ' + item.airtype + ' success')
        this.props.getAirType()
        this.search()
      }
    })
  }

  search = () => {
    let { form } = this.state
    let param = ''
    param += 'airType=' + (form.airTypeID || '')
    param += '&grossWeight=' + (form.grossweight || '')
    param += '&pax=' + (form.pax || '')
    this.getList(param)
  }

  onChange = ({ name, value }) => {
    let { form } = this.state
    form[name] = value
    this.setState({ form, data: [] }, this.search)
  }

  refresh = () => {
    this.props.onRefreshDone()
    setTimeout(() => { this.search() }, 200)
  }

  onCheckChange = (item, checked) => {
    const body = {
      airTypeId: item.airTypeId,
      statusDisplay: checked ? 1 : 0
    }
    this.props.updateDisplayStatusAirType(body).then(res => {
      if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
        message.success('Update status success')
        this.search()
      }
    })
  }

  render() {
    const { form, data, isLoading } = this.state
    const { refresh } = this.props
    if (refresh) this.refresh()
    //const { language } = this.props
    //const name = context[language].viewStatModal
    const readOnly = isReadOnly('airtypesetting')
    const template = [{
      type: 'select',
      name: 'airTypeID',
      value: form.airTypeID,
      placeholder: 'Select Air Type...',
      options: {
        name: 'airTypeOptions',
        id: 'airTypeID',
        text: 'airType',
        filter: (thing, index, self) => index === self.findIndex((t) => (t.id === thing.id))
      }
    }, {
      type: 'search',
      name: 'grossweight',
      value: form.grossweight,
      placeholder: 'Gross Weight...',
    }, {
      type: 'search',
      name: 'pax',
      value: form.pax,
      placeholder: 'Pax...',
    }]
    const columns = [
      {
        title: '#',
        key: 'no',
        dataIndex: 'no',
      }, {
        title: 'Air Type',
        dataIndex: 'airtype',
        key: 'airtype'
      }, {
        title: 'Display(On/Off)',
        key: 'display',
        render: item => <Switch
          disabled={readOnly}
          onChange={checked => this.onCheckChange(item, checked)}
          checked={item.display} />
      }, {
        title: 'Update Date',
        dataIndex: 'updateDate',
        key: 'updateDate'
      }, {
        title: 'Edit',
        key: 'edit',
        render: item => (
          <button disabled={readOnly} onClick={() => this.edit(item)} type="button" className="btn btn-outline-success btn-sm">
            <FontAwesomeIcon icon="pen" />
          </button>
        )
      }, {
        title: 'Delete',
        key: 'delete',
        render: item => (
          readOnly ? <button disabled type="button" className="btn btn-outline-danger btn-sm">
            <FontAwesomeIcon icon="trash" />
          </button> :
            <Popover
              visible={this.state.visible[item.key]}
              onVisibleChange={_visible => {
                let { visible } = this.state
                visible[item.key] = _visible
                this.setState({ visible })
              }}
              content={<button
                type="button"
                className="btn btn-outline-danger btn-sm"
                onClick={() => this.onDelete(item)}>Delete</button>}
              title="Are you sure?"
              trigger="click">
              <button disabled={readOnly} type="button" className="btn btn-outline-danger btn-sm">
                <FontAwesomeIcon icon="trash" />
              </button>
            </Popover>
        )
      }
    ]

    const table = <Table
      scroll={{ x: '1500px' }}
      pagination={{
        defaultPageSize: 10,
        position: 'bottom'
      }}
      footer={isLoading ? () => <div className="text-center"><Spin className="pr-2" />loading...</div> : null}
      className="tbody-center bg-white"
      columns={columns}
      expandedRowRender={record => {
        return record.list.map((l, i) => (
          <div className="form-inline mb-2" key={i}>
            <div className="col-md-1 text-right">{i + 1}<Divider type="vertical" /></div>
            <div className="col-md-3 text-left">Gross Weight :{l.grossWeight}</div>
            <div className="col-md-2 text-left">Pax :{l.pax}</div>
          </div>
        ))
      }}
      dataSource={data} />
    return (
      <div className="card card-body card-body-component p-0">
        <div className="card card-body card-header-table">
          <div className="form-row form-inline col-md-12 mb-3 px-0">
            <div className="col-md-3">
              {!readOnly && <button type="button" onClick={this.add} className="btn btn-success">
                <FontAwesomeIcon icon="plus" /> Add Air Type
            </button>}
            </div>
            <div className="col-md-9 form-inline justify-content-end px-0">
              {template.map((t, i) => (<div className="col-md-2 justify-content-end pr-0" key={i}>
                {Render(t, this.onChange)}
              </div>))}
              <button onClick={() => this.setState({ form: {} }, this.search)} className="btn btn-sm btn-dark ml-2">
                <FontAwesomeIcon icon="redo" />
                <span className="ml-2">clear</span>
              </button>
            </div>
          </div>
          {table}
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  getListSettingAirtype: param => dispatch(getListSettingAirtype(param)),
  deleteAirType: id => dispatch(deleteAirType(id)),
  getAirType: () => dispatch(getAirType()),
  updateDisplayStatusAirType: body => dispatch(updateDisplayStatusAirType(body))
})

export default connect(mapStateToProps, mapDispatchToProps)(AirType)
