import React, { Component } from 'react'
import Render from '../../components/Renderer'
import { Divider, message } from 'antd'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { Table } from 'antd'
import { getDetailFormManagement, addFormManagement, updateFormManagement } from '../../redux/actions/formmanagement'
import { connect } from 'react-redux'
import { Constraints, table as tConstraints } from './Constraints'
import validatejs from 'validate.js'

class FormEdit extends Component {
  state = {
    form: {},
    data: [{ key: 1, isError: true }],
    deleted: [],
    disabledSave: false
  }

  componentDidMount() {
    const { match } = this.props
    if (match.params.id) {
      this.props.getDetailFormManagement(match.params.id).then(res => {
        this.setState({ isLoading: false })
        if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
          let result = res.payload.data.data
          let data = result.detailField ? result.detailField.slice() : []
          data.push({ isError: true })
          data.forEach((x, i) => x.key = i)
          delete result.detailField
          result.display = result.isEnable === 1
          this.setState({ form: result, data })
        }
      })
    } else {
      this.setState({ errors: validatejs({}, Constraints) })
    }
  }

  onBack = () => {
    const { history } = this.props
    history.goBack()
  }

  renderInput = (name, text, index) => {
    var template = {
      type: 'input',
      name: name,
      value: text,
      placeholder: '',
    }
    return Render(template, ({ name, value }) => this.onChangeTable(index, name, value))
  }

  renderSelect = (name, text, index) => {
    var template = {
      type: 'select',
      name: name,
      value: text,
      placeholder: '',
      options: {
        name: 'columnTypeOptions'
      }
    }
    return Render(template, ({ name, value }) => this.onChangeTable(index, name, value))
  }

  onChange = ({ name, value, error }) => {
    let { form, errors } = this.state
    form[name] = value
    if (errors === undefined) errors = {}
    if (error === undefined) delete errors[name]
    else errors[name] = error.join()
    if (Object.keys(errors).length === 0) errors = undefined
    this.setState({ form, errors })
  }

  onDelete = index => {
    let { data, deleted } = this.state
    if (data[index].airTypeId !== undefined) deleted.push(data[index])
    data.splice(index, 1)
    this.setState({ data, deleted })
  }

  onAdd = () => {
    let { data } = this.state
    data.push({ isError: true })
    data.forEach((x, i) => x.key = i)
    this.setState({ data })
  }

  onChangeTable = (index, name, value) => {
    let { data } = this.state
    data[index][name] = value
    for (let item of data) {
      const valid = validatejs(item, tConstraints)
      item.isError = valid !== undefined
    }
    this.setState({ data })
  }

  onSave = () => {
    this.setState({ disabledSave: true }, () => {
      const { match } = this.props
      const { form, data, deleted } = this.state
      const clean = data.filter(x => !x.isError)
      if (!match.params.id) {
        let body = {
          "nameTable": form.nameTable,
          "isEnable": form.display ? 1 : 0,
          "detailField": clean.map(x => ({
            "fieldName": x.fieldName,
            "typeField": x.typeField
          }))
        }
        this.props.addFormManagement(body).then(res => {
          if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
            message.success('Add Form success')
            this.onBack()
          }
        })
      } else {
        let body = {
          "formId": form.formId,
          "nameTable": form.nameTable,
          "isEnable": form.display ? 1 : 0,
          "detailField": clean.map(x => ({
            "detaileFormId": x.detaileFormId,
            "fieldName": x.fieldName,
            "typeField": x.typeField
          }))
        }
        body.detailField.forEach(x => x.detaileFormId === undefined && delete x.detaileFormId)
        deleted.forEach(x => {
          body.detailField.push({
            IsDelete: 1,
            "fieldName": x.fieldName,
            "typeField": x.typeField
          })
        })
        this.props.updateFormManagement(body).then(res => {
          this.setState({ disabledSave: false })
          if (res && res.payload.status === 200 && res.type.endsWith('SUCCESS')) {
            message.success('Update Form success')
            this.onBack()
          }
        })
      }
    })
  }

  render() {
    const { form, data, errors, disabledSave } = this.state
    const template = [{
      type: 'input',
      name: 'nameTable',
      label: 'Form Name',
      constraint: Constraints,
      value: form.nameTable,
    }, {
      type: 'switch',
      name: 'display',
      label: 'Display (On/Off)',
      value: form.display
    }]
    const columns = [
      {
        title: 'Column Name',
        key: 'fieldName',
        dataIndex: 'fieldName',
        width: 150,
        render: (text, row, index) => this.renderInput('fieldName', row.fieldName, index)
      }, {
        title: 'Column Type',
        dataIndex: 'typeField',
        key: 'typeField',
        width: 150,
        render: (text, row, index) => this.renderSelect('typeField', row.typeField, index)
      }, {
        title: 'Edit',
        key: 'edit',
        width: 80,
        render: (text, row, index) => <button
          onClick={() => {
            index + 1 === data.length ? this.onAdd(index) : this.onDelete(index)
          }}
          type="button"
          className={"btn btn-sm " + (index + 1 === data.length ? 'btn-outline-success' : 'btn-outline-danger')}>
          <FontAwesomeIcon icon={index + 1 === data.length ? "plus" : "trash"} />
        </button>
      }
    ]
    const table = <Table
      style={{ minWidth: 600 }}
      pagination={false}
      className="tbody-center bg-white"
      columns={columns}
      rowClassName={(record, index) => {
        if (record.isError && index !== data.length - 1) return 'bg-danger'
      }}
      dataSource={data} />

    return (
      <div className="card card-body card-body-component">
        <button type="button" onClick={this.onBack} className="btn btn-sm btn-success mb-3" style={{ width: 100 }}>
          <FontAwesomeIcon icon="angle-double-left" />
          <span className="ml-2">Back</span>
        </button>

        {template.map((data, i) => {
          return <div key={i} className="form-row form-inline mb-2 col-12 px-0">
            <label className="col-lg-2 col-md-3 col-sm-12 col-12 justify-content-end">{data.label} :</label>
            <div className="col-lg-8 col-md-8 col-sm-12 col-12">
              {Render(data, this.onChange)}
            </div>
          </div>
        })}
        {table}
        <Divider />
        {/* footer */}
        <div className="text-center mt-2">
          <button
            onClick={this.onBack}
            className="btn btn-light mr-2">
            Cancel
          </button>
          <button
            disabled={errors !== undefined || disabledSave}
            onClick={this.onSave}
            className="btn btn-success mr-2">
            Save
          </button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  language: state.language
})

const mapDispatchToProps = dispatch => ({
  getDetailFormManagement: id => dispatch(getDetailFormManagement(id)),
  addFormManagement: body => dispatch(addFormManagement(body)),
  updateFormManagement: body => dispatch(updateFormManagement(body)),
})

export default connect(mapStateToProps, mapDispatchToProps)(FormEdit)
